import { getTableElement } from "app/table/utils";
import { EditEdge } from "diagram/edit/edit";
import * as React from "react";
import ResizeObserverPolyfill from "resize-observer-polyfill";

const SafeResizeObserver = window.ResizeObserver ?? ResizeObserverPolyfill;

const mutationCb = (setChanged: () => void): MutationCallback => (records) => {
    records.forEach((record) => {
        if (record.type !== "attributes") return;
        if (record.attributeName !== "data-is-visible") return;
        setChanged();
    });
};

const resizeCb = (setChanged: () => void): ResizeObserverCallback => () => {
    setChanged();
};

const mutationInit = { attributeFilter: ["data-is-visible"] };

export const useEdgeObserve = (edge: EditEdge): object => {
    // Watch for non-react dependencies (e.g. DOM rendering)
    const [changed, setChanged] = React.useState({});
    React.useLayoutEffect(() => {
        const from = getTableElement(edge.from.table);
        const to = getTableElement(edge.to.table);
        const set = () => setChanged({});
        // resize
        const resize = new SafeResizeObserver(resizeCb(set));
        resize.observe(from);
        resize.observe(to);
        // mutation
        const mutation = new MutationObserver(mutationCb(set));
        mutation.observe(from, mutationInit);
        mutation.observe(to, mutationInit);
        return () => {
            mutation.disconnect();
            resize.disconnect();
        };
    }, [edge]);

    return changed;
};
