import { ProgressCircle, background, DivPx } from "@moai/core";
import React from "react";
import s from "./progress.module.scss";
import { addExternalFn } from "external/external";

export type Progress = boolean | number;

const registerExternal = addExternalFn("setProgressIndicator");

export const ViewProgress = () => {
    const [progress, setProgress] = React.useState<Progress>(false);
    const [text, setText] = React.useState<string | undefined>(undefined);

    React.useEffect(() => {
        const fn = (progress: Progress, text?: string) => {
            setProgress(progress);
            setText(text);
        };
        registerExternal(fn);
    }, []);

    if (progress === false) return null;
    return (
        <div className={[s.container, background.secondary].join(" ")}>
            <div className={s.body}>
                <ProgressCircle
                    size={24}
                    value={progress === true ? null : progress}
                />
                {text !== undefined && (
                    <>
                        <DivPx size={16} />
                        <div>{text}</div>
                    </>
                )}
            </div>
        </div>
    );
};
