import { EditTable } from "diagram/edit/edit";

export const MAX_ROWS_COUNT = 50;
export const TABLE_CLIENT_WIDTH = 224;
export const TABLE_MARGIN_SINGLE = 48;
export const TABLE_MARGIN_DOUBLE = TABLE_MARGIN_SINGLE * 2;
export const TABLE_OFFSET_WIDTH = TABLE_CLIENT_WIDTH + TABLE_MARGIN_DOUBLE;

export const getTableElementId = (tableId: number) => `table . ${tableId}`;

export const getTableElement = (tableId: number): HTMLDivElement => {
    const element = document.getElementById(getTableElementId(tableId));
    if (element instanceof HTMLDivElement) return element;
    throw Error("Element is not HTMLDivElement");
};

interface Size {
    width: number;
    height: number;
}

export const getTableEstimatedSize = (
    table: EditTable,
    isRowsExpand?: boolean
): Size => {
    // We hide rows when there are more
    const count = isRowsExpand
        ? table.value.rows.length
        : Math.min(table.value.rows.length, MAX_ROWS_COUNT + 1);
    // Use an estimated value based on the number of rows (these values
    // come from manually measuring the initial render)
    return { width: TABLE_CLIENT_WIDTH, height: count * 24 + 64 };
};

export const getTableSize = (table: EditTable): Size => {
    // Try to get exact size if possible
    try {
        const element = getTableElement(table.id);
        // Double check to avoid using one from previous diagram. This happens
        // when import a new layout
        if (element.dataset.name !== table.value.name)
            throw Error("Outdated table from previous diagram");
        return { width: element.clientWidth, height: element.clientHeight };
    } catch (e) {
        // If can't find the element then use estimated size
        // (This happens when the algo is run before rendering, like in import)
        return getTableEstimatedSize(table);
    }
};
