import { DivPx } from "@moai/core";
import { SetState } from "@tableplus/toolkit/src/types/types";
import { RelatedEdge } from "app/view/canvas/canvas";
import { EditColumn, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { TableBodyAdd } from "./add/add";
import s from "./body.module.scss";
import { navigateViaArrowKeys } from "./events/navigate";
import { TableBodyHeader } from "./header/header";
import { TableBodyRows } from "./rows/rows";

interface Props {
    // Table
    table: EditTable;
    // Edit
    isEdit: boolean;
    setIsEdit: SetState<boolean>;
    // Diagram
    columns: EditColumn[];
    isRowsExpand: boolean;
    diagramDispatch: React.Dispatch<DiagramAction>;
    relatedEdges: RelatedEdge[];
}

const onFocus = (props: Props) => (event: React.FocusEvent) => {
    const related = event.relatedTarget as Node | null;
    if (event.currentTarget.contains(related)) return;
    if (event.target instanceof HTMLButtonElement) return;
    props.setIsEdit(true);
};

export const TableBody = (props: Props) => {
    const [isExpand, setIsExpand] = React.useState(false);

    React.useEffect(() => {
        if (!props.isEdit) setIsExpand(false);
    }, [props.isEdit]);

    return (
        <div className={s.container} onFocus={onFocus(props)}>
            <DivPx size={8} />

            {/* HEADER */}
            {props.isEdit && (
                <div className={s.header}>
                    <TableBodyHeader
                        columns={props.columns}
                        isExpand={isExpand}
                        setIsExpand={setIsExpand}
                    />
                </div>
            )}

            {/* BODY */}
            <div onKeyDown={navigateViaArrowKeys}>
                <TableBodyRows
                    // rows
                    rows={props.table.value.rows}
                    // table
                    tableId={props.table.id}
                    isTableEdit={props.isEdit}
                    isTableExpand={isExpand}
                    // diagram
                    columns={props.columns}
                    isRowsExpand={props.isRowsExpand}
                    relatedEdges={props.relatedEdges}
                    diagramDispatch={props.diagramDispatch}
                />
            </div>

            {/* FOOTER */}
            {(props.isEdit || props.table.value.rows.length === 0) && (
                <TableBodyAdd
                    tableId={props.table.id}
                    diagramDispatch={props.diagramDispatch}
                />
            )}

            <DivPx size={8} />
        </div>
    );
};
