import * as Raw from "diagram/diagram";
import * as Edit from "diagram/edit/edit";
import { applyAutoLayout } from "diagram/layout/layout";

// Prepared maps from table's [name, schema] to EDIT ids to speed up EditRef
// conversion
type Ids = { table: number; rows: Map<string, number> };
type IdsMap = Map<string, Ids>; // string here is "<schema> . <name>"

const makeIdsMap = (
    fallbackSchema: string,
    tables: Edit.EditTable[]
): IdsMap => {
    const idsMap = new Map<string, Ids>();
    tables.forEach((table) => {
        const rows = new Map<string, number>();
        table.value.rows.forEach((row) => void rows.set(row.value.id, row.id));
        const schema = table.value.schema ?? fallbackSchema;
        const tuple = `${schema} . ${table.value.name}`;
        idsMap.set(tuple, { table: table.id, rows });
    });
    return idsMap;
};

const makeRefSide = (
    fallbackSchema: string,
    map: IdsMap,
    side: Raw.RefSide
): Edit.EditRefSide => {
    const schema = side.schema ?? fallbackSchema;
    const ids = map.get(`${schema} . ${side.name}`);
    if (ids === undefined) throw Error(`Table Ids "${side.name}" not found`);
    const rows = side.rows.map((editId) => {
        const realId = ids.rows.get(editId);
        if (realId === undefined) throw Error(`Row id "${editId}" not found`);
        return realId;
    });
    return { table: ids.table, rows };
};

const makeTable = (raw: Raw.Item, index: number): Edit.EditTable => {
    const rows = raw.rows.map((value, id) => ({ id, value }));
    const { name, id, schema } = raw;
    const value: Edit.EditTableValue = { rows, name, id, schema };
    const position = { top: 0, left: 0 }; // we will provide below
    return { value, position, id: index };
};

const makeColumn = (raw: Raw.Column, index: number): Edit.EditColumn => {
    return { id: index, value: raw };
};

const makeRef = (schema: string, idsMap: IdsMap) => {
    return (raw: Raw.Ref, index: number): Edit.EditRef => ({
        id: index,
        from: makeRefSide(schema, idsMap, raw.from),
        to: makeRefSide(schema, idsMap, raw.to),
    });
};

export const makeEditFromDiagram = (diagram: Raw.Diagram): Edit.EditDiagram => {
    const schema = diagram.schema;
    const columns: Edit.EditColumn[] = diagram.columns.map(makeColumn);
    const tables: Edit.EditTable[] = diagram.items.map(makeTable);
    const idsMap = makeIdsMap(schema, tables);
    const refs: Edit.EditRef[] = diagram.refs.map(makeRef(schema, idsMap));
    const size = { width: 0, height: 0 };
    const edit: Edit.EditDiagram = { tables, refs, columns, size, schema };
    return applyAutoLayout(edit);
};
