import { selectOnRefClick } from "app/view/select/edge";
import { SetSelectIds } from "app/view/select/select";
import { EditEdge, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { debugLog } from "utils/debug";
import s from "./edge.module.scss";
import { EdgeFrom } from "./from";
import { useEdgeState } from "./state/state";
import { EdgeTo } from "./to";
import { getEdgeElementId, getEdgeSubElementId } from "./utils";
import { EdgeHighlight } from "./highlight";
import { EdgesMids } from "./state/mid";

interface Props {
    edge: EditEdge;
    // Tables
    fromTable: EditTable;
    toTable: EditTable;
    // Diagram
    edgesMids: React.MutableRefObject<EdgesMids>;
    diagramDispatch: React.Dispatch<DiagramAction>;
    // Select
    isSelected: boolean;
    setSelectIds: SetSelectIds;
}

const getClassName = (props: Props): string => {
    const array = [s.container];
    if (props.isSelected) array.push(s.selected);
    return array.join(" ");
};

const EdgePure = (props: Props) => {
    debugLog("Edge", props.edge.from.rows, props.edge.to.rows);

    const { edge, fromTable, toTable, edgesMids } = props;
    const draw = useEdgeState({ edge, fromTable, toTable, edgesMids });
    if (draw === null) return null;
    const { path, points } = draw;

    return (
        <g id={getEdgeElementId(props.edge.id)} className={getClassName(props)}>
            <path
                id={getEdgeSubElementId(edge.id, "path-main")}
                className={s.path}
                d={path.main}
            />
            <path className={s.path} d={path.from} />
            <path className={s.path} d={path.to} />
            <EdgeFrom edge={props.edge} points={points.from} />
            <EdgeTo edge={props.edge} points={points.to} />
            {props.isSelected && (
                <EdgeHighlight edge={props.edge} path={path} />
            )}
            <path
                className={s.hitarea}
                d={path.main}
                onClick={selectOnRefClick({
                    refId: props.edge.id,
                    setSelectIds: props.setSelectIds,
                })}
            />
        </g>
    );
};

export const Edge = React.memo(EdgePure);
