import { SetState } from "@tableplus/toolkit/src/types/types";
import { EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import s from "./header.module.scss";
import { HeaderName } from "./name/name";
import { TableHeaderRemove } from "./remove/remove";

interface Props {
    table: EditTable;
    isEdit: boolean;
    setIsEdit: SetState<boolean>;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const TableHeader: React.FC<Props> = (props) => (
    <div className={`${s.container} ${props.isEdit ? s.edit : ""}`}>
        <div className={s.main}>
            <HeaderName
                table={props.table}
                setIsEdit={props.setIsEdit}
                diagramDispatch={props.diagramDispatch}
            />
            <div className={s.remove}>
                <TableHeaderRemove
                    tableId={props.table.id}
                    diagramDispatch={props.diagramDispatch}
                />
            </div>
        </div>
    </div>
);
