import { background, borderColor, Button, DivPx, Icon, text } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { SetState } from "@tableplus/toolkit/src/types/types";
import { EditColumn } from "diagram/edit/edit";
import React from "react";
import s from "./header.module.scss";

interface Props {
    columns: EditColumn[];
    isExpand: boolean;
    setIsExpand: SetState<boolean>;
}

const className = [
    background.secondary,
    borderColor.weak,
    s.container,
    text.muted,
].join(" ");

const renderColumn = (column: EditColumn, index: number) => {
    const cls = [s.field, index === 1 ? s.rightField : ""].join(" ");
    return <div key={column.id} className={cls} children={column.value.name} />;
};

const getExpandIcon = (isExpand: Boolean): string =>
    isExpand ? icon.doubleChevronLeft : icon.doubleChevronRight;

export const TableBodyHeader = (props: Props) => (
    <div className={className}>
        <DivPx size={8} />

        {/* PADDING */}
        <div className={Button.size.small.main}>
            <Icon path={icon.blank} size={Button.size.small.iconSize} />
        </div>

        {/* COLUMN */}
        {props.columns
            .slice(0, props.isExpand ? undefined : 2)
            .map(renderColumn)}

        {/* EXPAND */}
        <Button
            onClick={() => props.setIsExpand((b) => !b)}
            style={Button.style.flat}
            size={Button.size.small}
            icon={getExpandIcon(props.isExpand)}
        />

        <DivPx size={8} />
    </div>
);
