import { EdgePoint, EdgePoints } from "./point";

/**
 * This model is not a source of data as it contains duplicated information
 * (e.g. "length" can be derived from "d"). In fact, the whole interface is a
 * derived data from an "edge" (which is a source of truth).
 *
 * Therefore, the interface should be read-only and used to aid rendering only.
 */
export interface EdgePath {
    /** The main path that connects first "from.row" to first "to.row" */
    main: string;
    /** Length of the main path */
    length: number;
    /** Connect from outer to from inners */
    from: string;
    /** Connect to outer to to inners */
    to: string;
}

const getPathD = (points: EdgePoint[]): string => {
    if (points.length === 0) return "";
    if (points.length === 1) throw Error("points.length must be 0 or 2+");
    return [
        `M${points[0].x},${points[0].y}`,
        ...points.slice(1).map((point) => `L${point.x},${point.y}`),
    ].join(" ");
};

const getPathLength = (points: EdgePoint[]): number =>
    points.reduce((prev, point, index) => {
        if (index === 0) return 0;
        const { x: x1, y: y1 } = point;
        const { x: x2, y: y2 } = points[index - 1];
        return prev + Math.sqrt(Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2));
    }, 0);

export const getEdgePath = (points: EdgePoints): EdgePath => {
    const { from, mid, to } = points;
    // prettier-ignore
    const mainPoints = [from.inners[0], ...mid, to.inners[0]];
    const fromPoints = from.inners.slice(1).flatMap((p) => [p, from.outer]);
    const toPoints = to.inners.slice(1).flatMap((p) => [p, to.outer]);

    return {
        main: getPathD(mainPoints),
        length: getPathLength(mainPoints),
        from: getPathD(fromPoints),
        to: getPathD(toPoints),
    };
};
