import { TablePosition } from "diagram/edit/layout";
import { DiagramReducer } from "../reducer";

export interface SetTablePositionPayload {
    id: number;
    position: TablePosition;
}

type Reducer = DiagramReducer<SetTablePositionPayload>;

export const setTablePosition: Reducer = (diagram, payload) => ({
    ...diagram,
    tables: diagram.tables.map((table) => {
        if (table.id !== payload.id) return table;
        return { ...table, position: payload.position };
    }),
});
