import { Input } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { getTableElement } from "app/table/utils";
import { SetSelectIds } from "app/view/select/select";
import { getTableRelatedIds } from "app/view/select/table";
import { EditDiagram } from "diagram/edit/edit";
import React from "react";
import { HeaderInputProps } from "./header";

interface Props extends HeaderInputProps {
    diagram: EditDiagram;
    setSelectIds: SetSelectIds;
}

const scrollOption: ScrollIntoViewOptions = {
    behavior: "smooth",
    block: "center",
    inline: "center",
};

const focusTable = (
    diagram: EditDiagram,
    setSelectIds: SetSelectIds,
    name: string
) => {
    // Select the table in state
    const table = diagram.tables.find((table) => table.value.name === name);
    // Throwing error here because we already check in the useEffect
    if (table === undefined) throw Error("table is undefined");
    setSelectIds(getTableRelatedIds(table.id, diagram.refs));
    // Scroll the element into view
    getTableElement(table.id).scrollIntoView(scrollOption);
};

export const HeaderSearch = (props: Props) => {
    const { diagram, setSelectIds } = props;

    const tables: string[] = React.useMemo(() => {
        const set = new Set<string>();
        diagram.tables.forEach((table) => void set.add(table.value.name));
        return [...set];
    }, [diagram.tables]);

    const [value, _setValue] = React.useState("");

    const focusIfNecessary = (name: string) => {
        if (tables.includes(name) === false) return;
        focusTable(diagram, setSelectIds, name);
    };

    return (
        <div style={{ flex: "1 1 0px" }}>
            <Input
                icon={icon.search}
                placeholder="Find table"
                value={value}
                setValue={(newValue) => {
                    _setValue(newValue);
                    focusIfNecessary(newValue);
                }}
                list={{ id: "header-search", values: tables }}
                onKeyUp={(event) => {
                    if (event.key !== "Enter") return;
                    focusIfNecessary(value);
                }}
                size={props.inputSize}
            />
        </div>
    );
};
