import { getTableElement } from "app/table/utils";
import { EditDiagram, EditRef } from "diagram/edit/edit";
import { TablePosition } from "diagram/edit/layout";
import { SelectId, SelectIdType } from "../../select";
import { SelectBox } from "./drag";

const isBoxOverlap = (a: SelectBox, b: SelectBox) => {
    // If one rectangle is on the left side of other
    if (a.left > b.left + b.width) return false;
    if (b.left > a.left + a.width) return false;
    // If one rectangle is above the other
    if (a.top > b.top + b.height) return false;
    if (b.top > a.top + a.height) return false;
    // Otherwise it overlaps
    return true;
};

export const isTableOverlap = (props: {
    target: SelectBox;
    position: TablePosition;
    element: HTMLDivElement;
}) => {
    if (props.element.dataset.isVisible !== "true") return false;
    // Get box of "table"
    const { clientWidth: width, clientHeight: height } = props.element;
    const { top, left } = props.position;
    const current: SelectBox = { width, height, top, left };
    // Compare and set
    return isBoxOverlap(props.target, current);
};

interface Props {
    diagram: EditDiagram;
}

const getOverlapTables = (props: Props, target: SelectBox): number[] => {
    return props.diagram.tables
        .filter((table) =>
            isTableOverlap({
                target,
                position: table.position,
                element: getTableElement(table.id),
            })
        )
        .map((table) => table.id);
};

const getOverlapRefs = (props: Props, tables: number[]): number[] => {
    const set = new Set(tables);
    const isRelated = (ref: EditRef) => {
        return set.has(ref.from.table) || set.has(ref.to.table);
    };
    return props.diagram.refs.filter(isRelated).map((ref) => ref.id);
};

const getIds = (ids: number[], type: SelectIdType) =>
    ids.map((value) => ({ type, value }));

export const getOverlapIds = (props: Props, box: SelectBox): SelectId[] => {
    const tables = getOverlapTables(props, box);
    const refs = getOverlapRefs(props, tables);
    return [...getIds(tables, "table"), ...getIds(refs, "ref")];
};
