import { EdgeBox, EdgeBoxes } from "./box";
import { avoidMidCollision, EdgesMids } from "./mid";
import { EditEdge } from "diagram/edit/edit";

// TL;DR: From Box (top, left) to Point (x, y)!

export interface EdgePoint {
    x: number;
    y: number;
}

type Side = "left" | "right";

/**
 * Points of "from" and "to" (where an Edge starts and ends)
 *
 * Let's say a path is drawn from id1 and id2 rows (composite keys). In this
 * case, the path will start from B and C, then join at A, then go outside of
 * the table:
 *
 *       . . . . . . . . . . . . . .
 *       .
 *       .   |------------------|
 *       .   |    user table    |
 *       .   |------------------|
 *       A   B  id1       uuid  |
 *       .   C  id2       uuid  |
 *
 * A:       outer
 * B & C:   inners
 */
export interface EdgePointsEnd {
    inners: EdgePoint[];
    outer: EdgePoint;
    side: Side;
}

/** A complete set of points to draw an Edge */
export interface EdgePoints {
    from: EdgePointsEnd;
    mid: EdgePoint[];
    to: EdgePointsEnd;
}

/** Avoid line going into or along the border of a table */
const SAFE_SPACE = 32;

const toPoint = (side: Side) => (box: EdgeBox): EdgePoint => ({
    x: side === "left" ? box.left : box.right,
    y: box.top,
});

const getOuterPoint = (inners: EdgePoint[], side: Side): EdgePoint => ({
    x: inners[0].x + (side === "right" ? 1 : -1) * SAFE_SPACE,
    y: inners[0].y,
});

/**
 * Decide which side of a table should we draw from. For simplicity this
 * returns EdgePoints but with empty "mid". The "mid" will be provide later.
 */
const getEndPoints = (boxes: EdgeBoxes): Omit<EdgePoints, "mid"> => {
    // Horizontal-wise, we only need to compare one row
    const [from, to] = [boxes.from[0], boxes.to[0]];
    let [fromSide, toSide]: Side[] = ["right", "right"];
    if (from.right <= to.left - SAFE_SPACE * 3) {
        [fromSide, toSide] = ["right", "left"];
    } else if (from.left >= to.right + SAFE_SPACE * 3) {
        [fromSide, toSide] = ["left", "right"];
    }
    const fromInners = boxes.from.map(toPoint(fromSide));
    const fromOuter = getOuterPoint(fromInners, fromSide);
    const toInners = boxes.to.map(toPoint(toSide));
    const toOuter = getOuterPoint(toInners, toSide);
    return {
        from: { inners: fromInners, side: fromSide, outer: fromOuter },
        to: { inners: toInners, side: toSide, outer: toOuter },
    };
};

/**
 * Return the middle points between 2 sides so that the path between them
 * doesn't have a diagonal line
 */
const getMidPoints = (points: Omit<EdgePoints, "mid">): EdgePoint[] => {
    const { from, to } = points;
    // When 2 ends are from different side, we need to pick mid points from the
    // middle area between them:
    //
    //   A o---X
    //         |
    //         Y---> B
    if (from.side !== to.side) {
        const midX = (from.outer.x + to.outer.x) / 2;
        return [
            { x: midX, y: from.outer.y },
            { x: midX, y: to.outer.y },
        ];
    }

    // When 2 ends are from the same side, we need to pick a mid point from
    // that side (to avoid the path from overlapping the target):
    //
    //   A o----X
    //          |
    //     B <--Y
    //
    // Note that we still need to produce two mid points even though the "safe"
    // point can already be a mid point. This is requires for the "avoid edge
    // collision algorithm" to work properly.
    if (from.side === to.side) {
        const midX = Math.max(from.outer.x, to.outer.x);
        return [
            { x: midX, y: from.outer.y },
            { x: midX, y: to.outer.y },
        ];
    }
    throw Error("never");
};

interface Props {
    edgesMids?: EdgesMids;
    edge: EditEdge;
    boxes: EdgeBoxes;
}

export const getEdgePoints = (props: Props): EdgePoints => {
    const { edgesMids, edge, boxes } = props;
    // Get points
    const { from, to } = getEndPoints(boxes);
    let mid = getMidPoints({ from, to });
    // Avoid collision if possible
    if (edgesMids !== undefined) {
        mid = avoidMidCollision({ edgesMids, edge, midPoints: mid });
    }
    return { from, mid, to };
};
