import React from "react";
import { addExternalFn } from "external/external";

export type Platform = "web" | "mac";

const webDomains = ["localhost", "diagramplus.com"];

const initial: Platform = (() => {
    if (webDomains.includes(window.location.hostname)) return "web";
    if (window.location.hostname.startsWith("192.168.1")) return "web";
    if (navigator.platform.toLowerCase().includes("mac")) return "mac";
    return "web";
})();

export const PlatformContext = React.createContext<Platform>(initial);

/**
 * The platform the app is running on. This is not navigator.platform, but to
 * separate between running directly in browsers and embedded in desktop apps
 */
export const usePlatform = () => {
    const platform: Platform = React.useContext(PlatformContext);
    return platform;
};

const registerExternal = addExternalFn("setPlatform");

export const usePlatformProvider = (): Platform => {
    const [platform, setPlatform] = React.useState<Platform>(initial);
    React.useEffect(() => {
        const external = (text: Platform): void => void setPlatform(text);
        registerExternal(external);
    }, []);
    return platform;
};
