import { applyAutoLayout } from "diagram/layout/layout";
import { DiagramReducer } from "../reducer";

export interface SetDiagramLayoutPayload {
    type: "auto";
}

export type DiagramLayoutAction = {
    type: "set-layout";
    payload: SetDiagramLayoutPayload;
};

export const diagramLayoutReducer: DiagramReducer = (diagram, action) => {
    switch (action.type) {
        case "set-layout":
            switch (action.payload.type) {
                case "auto":
                    return applyAutoLayout(diagram);
                default:
                    return diagram;
            }
        default:
            return diagram;
    }
};
