type VoidFn = (...args: any[]) => void;

// Functions for outside to use
const globalFns: { [name: string]: Function } = {};

const globalLogger: [string, any][] = [];

export const addExternalFnDirect = (name: string, fn: Function) => {
    globalFns[name] = fn;
};

export const addExternalFn = (name: string): ((fn: VoidFn) => void) => {
    let internalFn: VoidFn | null = null;
    const pendingArgs: any[][] = [];

    // External
    const externalFn = (...args: any[]) => {
        if (internalFn !== null) {
            globalLogger.push(["Execute", args]);
            return internalFn(...args);
        } else {
            globalLogger.push(["Pending", args]);
            pendingArgs.push(args);
        }
    };
    globalFns[name] = externalFn;

    // Register
    const registerFn = (fn: VoidFn) => {
        internalFn = fn;
        while (pendingArgs.length > 0) {
            const args = pendingArgs.shift();
            globalLogger.push(["Execute pending item", args]);
            (internalFn as VoidFn)(...args);
        }
    };
    return registerFn;
};

(window as any).Diagram = globalFns;
(window as any).Logger = globalLogger;
