import React from "react";
import { EditDiagram } from "../edit/edit";
import { useDiagramExternal } from "./external/external";
import { DiagramAction, diagramReducer } from "./reducer/reducer";
import { useDiagramStorage } from "./storage/storage";
import { useDiagramUndoShortcut } from "./undo/shortcut";
import * as Undo from "./undo/undo";

export interface DiagramState {
    history: Undo.UndoState<EditDiagram>;
    dispatch: React.Dispatch<DiagramAction | Undo.UndoAction>;
}

const undoReducer = Undo.createUndoReducer(diagramReducer);

export const useDiagram = (): DiagramState => {
    const storage = useDiagramStorage();
    const init = () => Undo.createUndoInitial(storage.get());
    const [history, dispatch] = React.useReducer(undoReducer, undefined, init);

    useDiagramExternal(history.present, dispatch);
    useDiagramUndoShortcut({ history, dispatch });

    const { set: storageSet } = storage;
    React.useEffect(() => {
        storageSet(history.present);
    }, [history.present, storageSet]);

    return { history, dispatch };
};
