import { Button, Icon } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { EditRef } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { AddRefPayload } from "diagram/state/reducer/ref/add";
import { RemoveRefPayload } from "diagram/state/reducer/ref/remove";
import React from "react";
import { DragSourceMonitor, useDrag } from "react-dnd";
import { getEmptyImage } from "react-dnd-html5-backend";
import { RowDragItem, RowDropResult } from "../drag";
import s from "./source.module.scss";

interface Props {
    tableId: number;
    rowId: number;
    diagramDispatch: React.Dispatch<DiagramAction>;
    edge: EditRef | null;
}

const beginDrag = (props: Props) => {
    if (props.edge === null) return;
    const payload: RemoveRefPayload = { id: props.edge.id };
    props.diagramDispatch({ type: "remove-ref", payload });
};

const collectDrag = (props: Props, monitor: DragSourceMonitor) => {
    if (monitor.isDragging() === false) return false;
    if (monitor.getItemType() !== "row") return false;
    const { tableId, rowId } = monitor.getItem() as RowDragItem;
    return props.tableId === tableId && props.rowId === rowId;
};

const endDrag = (props: Props, monitor: DragSourceMonitor) => {
    const result = monitor.getDropResult() as null | RowDropResult;
    if (result === null) return;
    const payload: AddRefPayload = {
        from: { table: props.tableId, rows: [props.rowId] },
        to: { table: result.tableId, rows: [result.rowId] },
    };
    props.diagramDispatch({ type: "add-ref", payload });
};

export const RowDragSource = (props: Props) => {
    const elementRef = React.useRef<HTMLDivElement>(null);

    const { rowId, tableId } = props;
    const [isDragging, dragRefCallback, previewRef] = useDrag({
        item: { type: "row", rowId, tableId, elementRef },
        begin: () => beginDrag(props),
        end: (_item, monitor) => endDrag(props, monitor),
        collect: (monitor) => collectDrag(props, monitor),
    });

    React.useEffect(() => {
        previewRef(getEmptyImage());
    }, [previewRef]);

    React.useEffect(() => {
        dragRefCallback(elementRef);
    }, [dragRefCallback]);

    const [isHover, setIsHover] = React.useState(false);

    const isActive = isDragging || isHover || props.edge !== null;
    const iconNode = (
        <Icon
            path={isActive ? icon.selection : icon.circle}
            size={Button.size.small.iconSize}
        />
    );

    return (
        <div
            ref={elementRef}
            className={s.container}
            onMouseEnter={() => setIsHover(true)}
            onMouseLeave={() => setIsHover(false)}
        >
            <div
                className={[
                    isDragging ? s.dragging : "",
                    Button.style.flat.main,
                    Button.size.small.main,
                    "react-draggable-cancel",
                ].join(" ")}
                children={iconNode}
            />
        </div>
    );
};
