import React from "react";
import dragscroll from "dragscroll";

import "./use-drag.scss";
import { SetState } from "@tableplus/toolkit/src/types/types";

interface State {
    drag: boolean;
    setDrag: SetState<boolean>;
    element: HTMLDivElement;
}

const onKeyUp = (state: State) => {
    const me = () => {
        state.setDrag(false);
        state.element.classList.remove("dragscroll");
        dragscroll.reset();
        state.element.removeEventListener("keyup", me);
    };
    return me;
};

const onKeyDown = (state: State) => (event: KeyboardEvent) => {
    if (event.target instanceof HTMLInputElement) return;
    if (event.key !== " ") return;
    event.preventDefault();
    if (state.drag) return;

    state.setDrag(true);
    state.element.classList.add("dragscroll");
    dragscroll.reset();
    state.element.addEventListener("keyup", onKeyUp(state));
};

export const useDragToScroll = (ref: React.RefObject<HTMLDivElement>) => {
    const [drag, setDrag] = React.useState(false);

    React.useEffect(() => {
        const element = ref.current;
        if (element === null) return;
        const state = { drag, setDrag, element };

        element.tabIndex = 0;
        const listener = onKeyDown(state);
        element.addEventListener("keydown", listener);
        return () => element.removeEventListener("keydown", listener);
    }, [drag, ref]);
};
