export const getEdgeElementId = (edgeId: number) => `edge . ${edgeId}`;

export const getEdgeElement = (edgeId: number): SVGGElement => {
    const element = document.getElementById(getEdgeElementId(edgeId));
    if (element instanceof SVGGElement) return element;
    throw Error("Element is not SVGGElement");
};

type Type = "path-main" | "from" | "to" | "highlight";
type Fn<T> = (edgeId: number, type: Type, index?: number) => T;

export const getEdgeSubElementId: Fn<string> = (edgeId, type, index) =>
    `edge . ${edgeId} . ${type} . ${index ?? 0}`;

export const getEdgeSubElement: Fn<SVGElement> = (edgeId, type, index) => {
    const id = getEdgeSubElementId(edgeId, type, index);
    const element = document.getElementById(id);
    if (element instanceof SVGElement) return element;
    throw Error("Element is not SVGElement");
};
