import * as React from "react";

const focusChildControl = (wrapper: Element): void => {
    const element = wrapper.querySelector("select,input,button,[tabindex]");
    if (element instanceof HTMLElement) return element.focus();
    throw Error("No focusable element under row-control");
};

const isControl = (element: Element): boolean =>
    element.classList.contains("row-control");

const getSibling = (element: Element, isPrevious: boolean): Element | null =>
    isPrevious ? element.previousElementSibling : element.nextElementSibling;

// Skip hidden rows group
const getNextParent = (curr: Element, isUp: boolean): Element | null => {
    const currParent = curr.closest(".table-row");
    if (currParent === null) throw Error(".table-row is null");
    let nextParent: Element | null = getSibling(currParent, isUp);
    while (nextParent !== null) {
        if (nextParent.classList.contains("table-row")) return nextParent;
        nextParent = getSibling(nextParent, isUp);
    }
    return null;
};

type Index = "first" | "last" | number;

const getChildControl = (parent: Element, index: Index): Element => {
    if (index === "first") {
        const first = parent.querySelector(".row-control");
        if (first !== null) return first;
    } else {
        const all = parent.querySelectorAll(".row-control");
        const control = all[index === "last" ? all.length - 1 : index];
        if (control !== undefined) return control;
    }
    throw Error(".table-row doesn't have .row-control");
};

const getControlIndex = (curr: Element): number => {
    const parent = curr.closest(".table-row");
    if (parent === null) throw Error(".table-row is null");
    const all = parent.querySelectorAll(".row-control");
    const index = [...all].indexOf(curr);
    if (index !== -1) return index;
    throw Error("curr is not a child of parent");
};

const getNextControl = (curr: Element, key: string): Element | null => {
    switch (key) {
        case "ArrowRight":
        case "ArrowLeft": {
            const isLeft = key === "ArrowLeft";
            const next = getSibling(curr, isLeft);
            if (next !== null && isControl(next)) return next;
            const nextParent = getNextParent(curr, isLeft);
            if (nextParent === null) return null;
            return getChildControl(nextParent, isLeft ? "last" : "first");
        }
        case "ArrowUp":
        case "ArrowDown": {
            const isUp = key === "ArrowUp";
            const nextParent = getNextParent(curr, isUp);
            if (nextParent === null) return null;
            const index = getControlIndex(curr);
            return getChildControl(nextParent, index);
        }
    }
    return null;
};

const navigate = (event: React.KeyboardEvent) => {
    const { target } = event;
    if (!(target instanceof HTMLElement)) return;
    if (target instanceof HTMLInputElement) return;
    // current control
    const control = target.closest(".row-control");
    if (control === null) return;
    // Prevent parent (viewport)'s scrolling
    event.preventDefault();
    const next = getNextControl(control, event.key);
    if (next === null) return;
    focusChildControl(next);
};

export const navigateViaArrowKeys = (event: React.KeyboardEvent) => {
    switch (event.key) {
        case "ArrowUp":
        case "ArrowRight":
        case "ArrowDown":
        case "ArrowLeft":
            return navigate(event);
    }
};
