import { encode } from "js-base64";
import { Platform } from "utils/platform";
import { ExportImageFile, ExportImageFormat } from "./image";

// TODO: Investigate more why the encoder can't encode these characters instead
// of manually maintaining this blacklist
const badWords = ["&%23xA;", "&%239;"];

const toBase64 = (file: ExportImageFile): string => {
    if (file.prefix.includes("base64")) return file.body;
    if (file.prefix.includes("utf-8")) {
        let clean = file.body;
        badWords.forEach((word) => {
            clean = clean.replaceAll(word, "");
        });
        return encode(clean);
    }
    throw Error(`Unknown file prefix: ${file.prefix}`);
};

const saveWithWebkit = (props: Props): void => {
    const message = {
        data: toBase64(props.file),
        ext: props.format,
        name: "diagram",
        title: "Save diagram as",
    };
    (window as any).webkit.messageHandlers.saveFile.postMessage(message);
};

const saveWithLink = (props: Props): void => {
    const link = document.createElement("a");
    link.download = `diagram.${props.format}`;
    link.href = `${props.file.prefix}${props.file.body}`;
    link.click();
};

interface Props {
    file: ExportImageFile;
    format: ExportImageFormat;
    platform: Platform;
}

export const saveExportImageFile = (props: Props): void => {
    switch (props.platform) {
        case "mac":
            return saveWithWebkit(props);
        case "web":
            return saveWithLink(props);
    }
};
