import { DiagramReducer } from "../reducer";

export interface RemoveRefPayload {
    id: number;
}

export interface RemoveMultipleRefsPayload {
    ids: number[];
}

type Payload = RemoveRefPayload;
type MultiplePayload = RemoveMultipleRefsPayload;

export const removeRef: DiagramReducer<Payload> = (diagram, payload) => {
    const refs = diagram.refs.filter((ref) => ref.id !== payload.id);
    return { ...diagram, refs };
};

type MultipleReducer = DiagramReducer<MultiplePayload>;
export const removeMultipleRefs: MultipleReducer = (diagram, payload) => {
    let next = diagram;
    payload.ids.forEach((id) => {
        next = removeRef(next, { id });
    });
    return next;
};
