import { background, Border, borderColor } from "@moai/core";
import { RowDragItem } from "app/row/drag/drag";
import { RelatedEdge } from "app/view/canvas/canvas";
import * as Select from "app/view/select/select";
import { EditColumn, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { DragLayerMonitor, useDragLayer } from "react-dnd";
import { debugLog } from "utils/debug";
import { TableBody } from "../body/body";
import { TableHeader } from "../header/header";
import { onTableBlur } from "./event";
import s from "./main.module.scss";

export interface Props {
    // Table
    table: EditTable;
    // Select
    isSelected: boolean;
    setSelectIds: Select.SetSelectIds;
    // Diagram
    isRowsExpand: boolean;
    relatedEdges: RelatedEdge[];
    columns: EditColumn[];
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const getCls = (props: Props, isEdit: boolean) => {
    const cls = [s.container, background.primary, borderColor.strong];
    if (props.isSelected) cls.push(s.selected);
    if (isEdit) cls.push(s.edit);
    return cls.join(" ");
};

const isMyRow = (props: Props) => (monitor: DragLayerMonitor): boolean => {
    const item = monitor.getItem();
    if (item === null) return false;
    if (item.type !== "row") return false;
    const rowItem = item as RowDragItem;
    return rowItem.tableId === props.table.id;
};

const toEdgeId = (edge: RelatedEdge): Select.SelectId => ({
    type: "ref",
    value: edge.edge.id,
});

const onFocus = (props: Props) => () => {
    props.setSelectIds((prev) => {
        const next: Select.SelectId[] = props.relatedEdges.map(toEdgeId);
        next.push({ type: "table", value: props.table.id });
        return Select.isSameSelectIds(prev, next) ? prev : next;
    });
};

const TableMainPure: React.FC<Props> = (props) => {
    debugLog("TableMain", props.table.id, props.table.value.id);
    const [isEdit, setIsEdit] = React.useState(false);
    const isRowDragging: boolean = useDragLayer(isMyRow(props));
    return (
        <div
            className={getCls(props, isEdit)}
            onBlur={onTableBlur(isRowDragging, setIsEdit)}
            tabIndex={0} // Avoid losing edit mode when dragging
            onFocus={onFocus(props)}
        >
            <TableHeader
                table={props.table}
                isEdit={isEdit}
                setIsEdit={setIsEdit}
                diagramDispatch={props.diagramDispatch}
            />
            <Border color="weak" />
            <div style={{ marginTop: -1 }} />
            <TableBody
                // table
                table={props.table}
                // Edit
                isEdit={isEdit}
                setIsEdit={setIsEdit}
                // Diagram
                isRowsExpand={props.isRowsExpand}
                relatedEdges={props.relatedEdges}
                diagramDispatch={props.diagramDispatch}
                columns={props.columns}
            />
        </div>
    );
};

export const TableMain = React.memo(TableMainPure);
