import { EditEdge } from "diagram/edit/edit";
import * as React from "react";
import s from "./edge.module.scss";
import { EdgePath } from "./state/path";
import { getEdgeSubElementId } from "./utils";

interface Props {
    edge: EditEdge;
    path: EdgePath;
}

export const EdgeHighlight = (props: Props) => (
    <circle className={s.dot} r="4">
        <animateMotion
            dur={`${props.path.length / 150}s`}
            repeatCount="indefinite"
            id={getEdgeSubElementId(props.edge.id, "highlight")}
            path={props.path.main}
        />
    </circle>
);
