import { EditRow } from "diagram/edit/edit";
import { MAX_ROWS_COUNT } from "app/table/utils";
import { EdgeFromRowMap } from "./edge-map";

export type TableRowsItem = EditRow | EditRow[];

export const groupTableRows = (
    rows: EditRow[],
    edgeMap: EdgeFromRowMap
): TableRowsItem[] => {
    // When slotCount reaches 0 then start grouping rows
    let slotCount = MAX_ROWS_COUNT - edgeMap.size;
    // Prepare
    const items: TableRowsItem[] = [];
    let prevGroup: EditRow[] = [];
    // Push row or group rows
    rows.forEach((row) => {
        if (slotCount > 0 || edgeMap.has(row.id)) {
            if (prevGroup.length !== 0) items.push(prevGroup);
            prevGroup = [];
            items.push(row);
            slotCount = slotCount - 1;
        } else {
            prevGroup.push(row);
        }
    });
    if (prevGroup.length !== 0) items.push(prevGroup);
    // Return
    return items;
};
