import { Button, ButtonMenu, ButtonMenuAction } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { HeaderButtonProps } from "../header";
import s from "./layout.module.css";

interface Props extends HeaderButtonProps {
    diagramDispatch: React.Dispatch<DiagramAction>;
    setIsRowsExpand: React.Dispatch<React.SetStateAction<boolean>>;
}

const layoutAction: DiagramAction = {
    type: "set-layout",
    payload: { type: "auto" },
};

const getActions = (props: Props): ButtonMenuAction[] => [
    {
        label: "Expand all columns and Arrange",
        fn: () => {
            props.setIsRowsExpand(true);
            window.setTimeout(() => props.diagramDispatch(layoutAction), 0);
        },
    },
    {
        label: "Collapse columns and Arrange",
        fn: () => {
            props.setIsRowsExpand(false);
            window.setTimeout(() => props.diagramDispatch(layoutAction), 0);
        },
    },
];

export const HeaderLayout = (props: Props) => (
    <div className={s.container}>
        <Button
            icon={icon.layoutAuto}
            onClick={() => void props.diagramDispatch(layoutAction)}
            children="Arrange"
            size={props.buttonSize}
        />
        <ButtonMenu
            actions={getActions(props)}
            icon={icon.caret}
            size={props.buttonSize}
        />
    </div>
);
