import { Button, background } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { RemoveTablePayload } from "diagram/state/reducer/table/remove";
import React from "react";
import s from "./remove.module.css";

interface Props {
    tableId: number;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const TableHeaderRemove = (props: Props) => (
    <div className={[s.container, background.primary].join(" ")}>
        <Button
            style={Button.style.flat}
            onClick={() => {
                const payload: RemoveTablePayload = { id: props.tableId };
                props.diagramDispatch({ type: "remove-table", payload });
            }}
            icon={icon.cross}
            size={Button.size.small}
        />
    </div>
);
