import React from "react";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { RemoveRowPayload } from "diagram/state/reducer/row/remove";

interface Params {
    diagramDispatch: React.Dispatch<DiagramAction>;
    tableId: number;
    rowId: number;
}

type Event = React.KeyboardEvent<HTMLDivElement>;

const dispatchRemoveRow = (params: Params) => {
    const { tableId, rowId } = params;
    const payload: RemoveRowPayload = { tableId, rowId };
    params.diagramDispatch({ type: "remove-row", payload });
};

export const useRowKeyUpListeners = (params: Params) => {
    const isEmpty = React.useRef(false);
    return {
        onKeyDown: (event: Event) => {
            if (!["Delete", "Backspace"].includes(event.key)) return;
            if (!(event.target instanceof HTMLInputElement)) return;
            isEmpty.current = event.target.value === "";
        },
        onKeyUp: (event: Event) => {
            if (!["Delete", "Backspace"].includes(event.key)) return;
            if (event.target instanceof HTMLInputElement) {
                if (isEmpty.current) dispatchRemoveRow(params);
            } else {
                dispatchRemoveRow(params);
            }
        },
    };
};
