const getGenericId = <T>(ids: T[], makeId: (counter: number) => T): T => {
    const set = new Set(ids);
    let counter = 1;
    let id = makeId(counter);
    while (set.has(id)) {
        counter = counter + 1;
        id = makeId(counter);
    }
    return id;
};

export const getStringId = (ids: string[], prefix: string) =>
    getGenericId(ids, (counter) =>
        counter === 1 ? prefix : `${prefix}_${counter}`
    );

export const getNumberId = (ids: number[]) =>
    getGenericId(ids, (counter) => counter);
