import * as Utils from "app/table/utils";
import dagre from "dagre";
import { TablePosition } from "diagram/edit/layout";
import { LayoutDiagram } from "./layout";

const roundPoint = (num: number) => Math.round(num / 8) * 8;
const roundWidth = (num: number) =>
    Math.ceil(num / Utils.TABLE_OFFSET_WIDTH) * Utils.TABLE_OFFSET_WIDTH;

export const applyDagreeLayout = (diagram: LayoutDiagram): LayoutDiagram => {
    const g = new dagre.graphlib.Graph({ multigraph: true });
    g.setGraph({
        rankdir: "LR",
        nodesep: Utils.TABLE_MARGIN_DOUBLE,
        ranksep: Utils.TABLE_MARGIN_DOUBLE,
        marginx: Utils.TABLE_MARGIN_SINGLE,
        marginy: Utils.TABLE_MARGIN_SINGLE,
    });
    g.setDefaultEdgeLabel(() => ({}));

    // nodes
    diagram.tables.forEach((table) => {
        const { width, height } = Utils.getTableSize(table);
        g.setNode(table.id.toString(), { label: "", width, height });
    });

    // edges
    diagram.refs.forEach((ref) => {
        g.setEdge(ref.from.table.toString(), ref.to.table.toString());
    });

    dagre.layout(g);

    return {
        tables: diagram.tables.map((prev) => {
            const box = g.node(prev.id.toString()); // @TODO check undefined?
            // Note that dagre's x y is center, not top left
            const position: TablePosition = { top: 0, left: 0 };
            position.top = roundPoint(box.y - box.height / 2);
            position.left = roundPoint(box.x - box.width / 2);
            return { ...prev, position };
        }),
        refs: diagram.refs,
        size: {
            width: roundWidth(g.graph().width ?? -1), // @TODO throw Error?
            height: roundPoint(g.graph().height ?? -1),
        },
    };
};
