import { EditDiagram } from "../../edit/edit";
import { DiagramLayoutAction, diagramLayoutReducer } from "./layout/layout";
import { DiagramParseActions, diagramParseReducer } from "./parse/parse";
import { DiagramRefActions, diagramRefReducer } from "./ref/ref";
import { DiagramRowActions, diagramRowReducer } from "./row/row";
import { DiagramTableActions, diagramTableReducer } from "./table/table";

export type DiagramReducer<T = DiagramAction> = (
    diagram: EditDiagram,
    arg: T
) => EditDiagram;

export type DiagramAction =
    | DiagramTableActions
    | DiagramRowActions
    | DiagramRefActions
    | DiagramLayoutAction
    | DiagramParseActions;

export const diagramReducer: DiagramReducer = (prev, action) => {
    let next = prev;
    next = diagramTableReducer(next, action);
    next = diagramRowReducer(next, action);
    next = diagramRefReducer(next, action);
    next = diagramParseReducer(next, action);
    next = diagramLayoutReducer(next, action);
    return next;
};
