import { ButtonMenu, ButtonMenuAction } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { EditDiagram } from "diagram/edit/edit";
import React from "react";
import { usePlatform } from "utils/platform";
import { HeaderButtonProps } from "../header";
import { exportAsImage } from "./image/image";
import { exportAsSql, exportSqlDrivers } from "./sql/sql";

interface Props extends HeaderButtonProps {
    diagram: EditDiagram;
    setTableVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export const HeaderExport = (props: Props) => {
    const platform = usePlatform();
    const actions: ButtonMenuAction[] = [
        {
            label: "Save as Image (PNG)",
            fn: () => exportAsImage(props.setTableVisible, platform, "png"),
        },
        {
            label: "Save as Web page (SVG)",
            fn: () => exportAsImage(props.setTableVisible, platform, "svg"),
        },
        { label: "", fn: () => {}, disabled: true },
        ...exportSqlDrivers.map((driver) => ({
            label: `Export to ${driver}`,
            fn: () => exportAsSql(props.diagram, platform, driver),
        })),
    ];
    return (
        <ButtonMenu
            icon={icon.share}
            actions={actions}
            size={props.buttonSize}
        />
    );
};
