import { makeDiagramFromEdit } from "diagram/edit/convert/from";
import { EditDiagram } from "diagram/edit/edit";
import { Platform } from "utils/platform";

export const exportSqlDrivers = [
    "MySQL",
    "MariaDB",
    "SQLite",
    "PostgreSQL",
    "Cassandra",
    "Snowflake",
    "MicrosoftSQLServer",
];

export const exportAsSql = (
    edit: EditDiagram,
    platform: Platform,
    driver: string
): void => {
    if (platform === "web")
        return window.alert("Export to SQL requires TablePlus app");
    const message = { data: makeDiagramFromEdit(edit), driver };
    (window as any).webkit.messageHandlers.exportSQL.postMessage(message);
};
