import React from "react";
import { SetState } from "@tableplus/toolkit/src/types/types";
import { Switcher } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { HeaderButtonProps } from "./header";

interface Props extends HeaderButtonProps {
    viewportRef: React.RefObject<HTMLDivElement>;
    zoom: number;
    setZoom: SetState<number>;
}

type Value = "out" | "reset" | "in" | null;

const setValue = (props: Props) => (value: Value): void => {
    switch (value) {
        case "reset":
            return props.setZoom(1);
        case "in":
            if (props.zoom >= 2) return;
            return props.setZoom(props.zoom + 0.25);
        case "out":
            if (props.zoom <= 0.25) return;
            return props.setZoom(props.zoom - 0.25);
        default:
            throw Error(`Unknown value: "${value}"`);
    }
};

export const HeaderZoom = (props: Props) => (
    <Switcher<Value>
        value={null}
        setValue={setValue(props)}
        options={[
            { key: "out", icon: icon.zoomOut, value: "out" },
            { label: `${props.zoom * 100}%`, value: "reset" },
            { key: "in", icon: icon.zoomIn, value: "in" },
        ]}
        size={props.buttonSize}
    />
);
