import * as React from "react";

export type SelectIdType = "table" | "ref";

export interface SelectId {
    type: SelectIdType;
    value: number;
}

export type SetSelectIds = React.Dispatch<React.SetStateAction<SelectId[]>>;

interface SelectState {
    ids: SelectId[];
    setIds: SetSelectIds;
    // Derived values
    tables: Set<number>;
    refs: Set<number>;
}

const filterId = (ids: SelectId[], type: SelectIdType): number[] =>
    ids.filter((id) => id.type === type).map((id) => id.value);

export const getSelectSets = (ids: SelectId[]) => ({
    tables: new Set(filterId(ids, "table")),
    refs: new Set(filterId(ids, "ref")),
});

export const isSameSelectIds = (a: SelectId[], b: SelectId[]): boolean => {
    if (a.length !== b.length) return false;
    const { tables, refs } = getSelectSets(a);
    return b.every((id) => {
        if (id.type === "table" && tables.has(id.value)) return true;
        if (id.type === "ref" && refs.has(id.value)) return true;
        return false;
    });
};

export const useSelect = (): SelectState => {
    const [ids, setIds] = React.useState<SelectId[]>([]);
    const { tables, refs } = React.useMemo(() => getSelectSets(ids), [ids]);
    return { ids, setIds, tables, refs };
};
