import { getRowElement } from "app/row/dom";
import { getTableElement } from "app/table/utils";
import { EditEdge, EditEdgeSide, EditTable } from "diagram/edit/edit";
import { TablePosition } from "diagram/edit/layout";

export interface EdgeBox {
    top: number;
    left: number;
    right: number;
}

export interface EdgeBoxes {
    from: EdgeBox[];
    to: EdgeBox[];
}

const getOffsetUntil = (target: HTMLElement, container: Element) => {
    let [top, left, element] = [0, 0, target];
    do {
        [top, left] = [top + element.offsetTop, left + element.offsetLeft];
        const parent = element.offsetParent;
        if (!(parent instanceof HTMLElement)) break;
        element = parent;
    } while (element !== container);
    return { top, left };
};

const getEdgeSideBoxes = (
    edge: EditEdgeSide,
    tablePosition: TablePosition
): EdgeBox[] => {
    const { top: tableTop, left: tableLeft } = tablePosition;
    const table = getTableElement(edge.table);
    return edge.rows.map((rowId) => {
        const row = getRowElement(edge.table, rowId);
        if (row !== null) {
            // If row is rendered then use row
            const { top: rowTop, left: rowLeft } = getOffsetUntil(row, table);
            const top = rowTop + tableTop + row.clientHeight / 2;
            const left = rowLeft + tableLeft;
            return { top, left, right: left + row.clientWidth };
        } else {
            // If row is not rendered then use center of table
            return {
                top: tableTop + table.clientHeight / 2,
                left: tableLeft,
                right: tableLeft + table.clientWidth,
            };
        }
    });
};

interface Props {
    edge: EditEdge;
    fromTable: EditTable;
    toTable: EditTable;
}

export const getEdgeBoxes = (props: Props): EdgeBoxes => ({
    from: getEdgeSideBoxes(props.edge.from, props.fromTable.position),
    to: getEdgeSideBoxes(props.edge.to, props.toTable.position),
});
