import { Background, Border, Button } from "@moai/core";
import { getRowElementId } from "app/row/dom";
import { Row } from "app/row/row";
import { RelatedEdge } from "app/view/canvas/canvas";
import { EditRow, EditColumn } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import { EdgeFromRowMap, getTableRowsEdgeMap } from "./edge-map";
import { groupTableRows, TableRowsItem } from "./group";

export interface TableRowProps {
    // table
    tableId: number;
    isTableEdit: boolean;
    isTableExpand: boolean;
    // diagram
    columns: EditColumn[];
    isRowsExpand: boolean;
    relatedEdges: RelatedEdge[];
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export interface TableRowsProps extends TableRowProps {
    rows: EditRow[];
}

const renderRow = (props: TableRowProps, edgeMap: EdgeFromRowMap) => {
    return (row: EditRow) => (
        <div
            className="table-row" // For keyboard navigation
            id={getRowElementId(props.tableId, row.id)}
            key={row.id}
        >
            <Row
                // row
                row={row}
                edge={edgeMap.get(row.id) ?? null}
                // table
                tableId={props.tableId}
                isEdit={props.isTableEdit}
                isExpand={props.isTableExpand}
                // diagram
                columns={props.columns}
                diagramDispatch={props.diagramDispatch}
            />
        </div>
    );
};

const HiddenRows = (props: { count: number; show: () => void }) => (
    <Background color="secondary">
        <Border color="weak" />
        <div style={{ marginTop: -1 }} />
        <Button
            style={Button.style.flat}
            size={Button.size.small}
            isFullWidth
            onClick={() => props.show()}
            children={`${props.count} columns hidden`}
        />
        <div style={{ marginTop: -1 }} />
        <Border color="weak" />
    </Background>
);

const renderItem = (
    props: TableRowProps,
    edgeMap: EdgeFromRowMap,
    isRowsExpand: boolean,
    setIsRowExpand: React.Dispatch<React.SetStateAction<boolean>>
) => (item: TableRowsItem, index: number): JSX.Element =>
    Array.isArray(item) ? (
        isRowsExpand ? (
            <React.Fragment key={item[0].id}>
                {item.map(renderRow(props, edgeMap))}
            </React.Fragment>
        ) : (
            <HiddenRows
                key={`hidden-${index}`}
                count={item.length}
                show={() => setIsRowExpand(true)}
            />
        )
    ) : (
        renderRow(props, edgeMap)(item)
    );

const HideButton = (props: { hide: () => void }) => (
    <Button
        style={Button.style.flat}
        size={Button.size.small}
        isFullWidth
        onClick={() => props.hide()}
        children="Hide columns"
    />
);

export const TableBodyRows = (props: TableRowsProps) => {
    const [isRowsExpand, setIsRowsExpand] = React.useState(false);
    const edgeMap = getTableRowsEdgeMap(props.tableId, props.relatedEdges);
    const items: TableRowsItem[] = groupTableRows(props.rows, edgeMap);
    const render = renderItem(props, edgeMap, isRowsExpand, setIsRowsExpand);
    const canExpand = items.length !== props.rows.length;

    React.useEffect(() => {
        if (canExpand) setIsRowsExpand(props.isRowsExpand);
    }, [props.isRowsExpand, canExpand]);

    return (
        <>
            {items.map(render)}
            {isRowsExpand && <HideButton hide={() => setIsRowsExpand(false)} />}
        </>
    );
};
