import { Edge } from "app/edge/edge";
import { useEdgesMids } from "app/edge/state/mid";
import { SetSelectIds } from "app/view/select/select";
import { EditDiagram, EditRefSide, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import s from "./edges.module.css";

interface Props {
    diagram: EditDiagram;
    diagramDispatch: React.Dispatch<DiagramAction>;
    selectedRefs: Set<number>;
    setSelectIds: SetSelectIds;
    tablesMap: Map<number, EditTable>;
}

export const CanvasEdges = (props: Props) => {
    const getTable = (side: EditRefSide): EditTable => {
        const table = props.tablesMap.get(side.table);
        if (table !== undefined) return table;
        throw Error("Table is undefined");
    };

    const edgesMids = useEdgesMids({ edges: props.diagram.refs });

    return (
        <svg
            className={s.container}
            width={props.diagram.size.width}
            height={props.diagram.size.height}
        >
            {props.diagram.refs.map((edge) => (
                <Edge
                    key={edge.id}
                    edge={edge}
                    // Select
                    isSelected={props.selectedRefs.has(edge.id)}
                    setSelectIds={props.setSelectIds}
                    // Tables
                    fromTable={getTable(edge.from)}
                    toTable={getTable(edge.to)}
                    // Diagram
                    edgesMids={edgesMids}
                    diagramDispatch={props.diagramDispatch}
                />
            ))}
        </svg>
    );
};
