import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import { SelectId, SelectIdType } from "../select";

const filterIds = (ids: SelectId[], type: SelectIdType): number[] =>
    ids.filter((id) => id.type === type).map((id) => id.value);

interface Props {
    ids: SelectId[];
    diagramDispatch: React.Dispatch<DiagramAction>;
    event: React.KeyboardEvent;
}

export const catchDelete = ({ ids, event, diagramDispatch }: Props) => {
    if (["Backspace", "Delete"].includes(event.key) === false) return;
    if (event.target instanceof HTMLInputElement) return;
    if (event.target instanceof HTMLTextAreaElement) return;
    diagramDispatch({
        type: "remove-multiple-refs",
        payload: { ids: filterIds(ids, "ref") },
    });
    diagramDispatch({
        type: "remove-multiple-tables",
        payload: { ids: filterIds(ids, "table") },
    });
    event.stopPropagation();
};
