import React from "react";
import s from "./add.module.scss";
import { Button } from "@moai/core";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { AddRowPayload } from "diagram/state/reducer/row/add";
import { icon } from "@tableplus/toolkit/src/icon/icon";

interface Props {
    tableId: number;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const addRow = (props: Props) => () => {
    const payload: AddRowPayload = { tableId: props.tableId };
    props.diagramDispatch({ type: "add-row", payload });
};

export const TableBodyAdd = (props: Props) => (
    <div className={s.container}>
        <Button
            onClick={addRow(props)}
            icon={icon.add}
            size={Button.size.small}
            style={Button.style.flat}
            isFullWidth={true}
        >
            Add
        </Button>
    </div>
);
