import { text } from "@moai/core";
import { EditColumn, EditRow } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { UpdateRowPayload } from "diagram/state/reducer/row/update";
import React from "react";
import { RowInputBoolean } from "./boolean/boolean";
import s from "./field.module.scss";
import { RowInputString } from "./string/string";

interface Props {
    tableId: number;
    row: EditRow;
    column: EditColumn;
    diagramDispatch: React.Dispatch<DiagramAction>;
    isRight: boolean;
}

const updateRowField = (props: Props) => (value: string) => {
    const { tableId, row, column } = props;
    const [property, rowId] = [column.value.name, row.id];
    if (value === row.value[property]) return;
    const payload: UpdateRowPayload = { tableId, rowId, property, value };
    props.diagramDispatch({ type: "update-row", payload });
};

interface RowInputProps {
    value: string;
    setValue: (value: string) => void;
}

type RowInputComponent = (props: RowInputProps) => JSX.Element;

const getInput = (props: Props): RowInputComponent => {
    const { type } = props.column.value;
    switch (type) {
        case "boolean":
            return RowInputBoolean;
        case "string":
            return RowInputString;
        default:
            throw Error(`Unknown column type "${type}"`);
    }
};

export const RowFieldInput = (props: Props) => (
    <div className={props.isRight ? `${s.right} ${text.muted}` : ""}>
        {getInput(props)({
            value: props.row.value[props.column.value.name] ?? "",
            setValue: updateRowField(props),
        })}
    </div>
);
