import { EditDiagram } from "diagram/edit/edit";
import { getNumberId } from "../id";
import { getTableNewPosition } from "../table/add";
import { TablePosition, DiagramSize } from "diagram/edit/layout";
import { TABLE_MARGIN_DOUBLE } from "app/table/utils";

export const appendDiagram = (
    original: EditDiagram,
    target: EditDiagram
): EditDiagram => {
    if (original.columns.length !== target.columns.length)
        throw Error("Cannot merge diagrams with different columns");

    const ids = original.tables.map((t) => t.id);

    // @TODO should not have this fix
    const offset = getTableNewPosition(original.tables);
    offset.top = offset.top - TABLE_MARGIN_DOUBLE;
    offset.left = offset.left - TABLE_MARGIN_DOUBLE;

    const tables = [...original.tables];
    target.tables.forEach((table) => {
        // @TODO this is slow because we need to create new ids everytime
        // a faster way should be letting getId to accept Set
        const id = getNumberId(ids);
        ids.push(id); // Avoid using this id next time
        const position: TablePosition = {
            top: table.position.top + offset.top,
            left: table.position.left + offset.left,
        };
        tables.push({ ...table, id, position });
    });

    const width = offset.left + target.size.width;
    const size: DiagramSize = { ...original.size, width };
    return { ...original, tables, size };
};
