import { makeDiagramFromEdit } from "diagram/edit/convert/from";
import { EditDiagram } from "diagram/edit/edit";
import * as React from "react";
import { getSelectSets, SelectId } from "../select";

interface Props {
    ids: SelectId[];
    diagram: EditDiagram;
}

const catchCopy = (props: Props, event: ClipboardEvent) => {
    if (event.target instanceof HTMLInputElement) return;
    if (event.target instanceof HTMLTextAreaElement) return;
    if (props.ids.length === 0) return;
    const { tables: selected } = getSelectSets(props.ids);
    if (selected.size === 0) return;
    const edit: EditDiagram = {
        columns: props.diagram.columns,
        tables: props.diagram.tables.filter((table) => {
            return selected.has(table.id);
        }),
        refs: props.diagram.refs.filter((ref) => {
            return selected.has(ref.from.table) && selected.has(ref.to.table);
        }),
        size: { width: 0, height: 0 },
        schema: props.diagram.schema,
    };
    const diagram = makeDiagramFromEdit(edit);
    const text = JSON.stringify(diagram);
    event.clipboardData?.setData("text/plain", text);
    event.preventDefault();
};

// @TODO: These should not belong to "select"
export const useCanvasCatchCopy = (props: Props) => {
    const { ids, diagram } = props;
    React.useEffect(() => {
        const listener = (event: ClipboardEvent) => {
            return catchCopy({ ids, diagram }, event);
        };
        document.addEventListener("copy", listener);
        return () => document.removeEventListener("copy", listener);
    }, [ids, diagram]);
};
