import { EditTable } from "diagram/edit/edit";
import { DiagramReducer } from "../reducer";

export interface UpdateTableNamePayload {
    id: number;
    name: string;
}

type Payload = UpdateTableNamePayload;

const updateName = (payload: Payload) => (table: EditTable): EditTable => {
    if (table.id !== payload.id) return table;
    return { ...table, value: { ...table.value, name: payload.name } };
};

export const updateTableName: DiagramReducer<Payload> = (diagram, payload) => {
    const tables = diagram.tables.map(updateName(payload));
    return { ...diagram, tables };
};
