import { useDrop } from "react-dnd";
import { RowDropResult } from "../drag";

interface Params {
    tableId: number;
    rowId: number;
}

export const useRowDropTarget = (params: Params) =>
    useDrop({
        accept: "row",
        drop: () => {
            const { tableId, rowId } = params;
            const result: RowDropResult = { tableId, rowId };
            return result;
        },
        collect: (monitor) => monitor.isOver(),
    });
