import React from "react";
import { SetState } from "@tableplus/toolkit/src/types/types";
import { Switcher, SwitcherOption } from "@moai/core";
import { Theme } from "@tableplus/toolkit/src/prefs/theme/theme";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { HeaderButtonProps } from "./header";

interface Props extends HeaderButtonProps {
    theme: Theme;
    setTheme: SetState<Theme>;
}

const options: SwitcherOption<Theme>[] = [
    { value: "theme-light", icon: icon.flash, key: "light" },
    { value: "theme-auto", label: "System", key: "auto" },
    { value: "theme-dark", icon: icon.moon, key: "dark" },
];

export const HeaderTheme = (props: Props) => (
    <Switcher<Theme>
        value={props.theme}
        setValue={props.setTheme}
        options={options}
        size={props.buttonSize}
    />
);
