import { Select } from "@moai/core";
import * as React from "react";
import s from "./boolean.module.css";

interface Props {
    value: string;
    setValue: (value: string) => void;
}

const TRUE_PREFIX = ["T", "t", "1", "Y", "y"]; // true, 1, yes

const isBoolean = (text: string): boolean =>
    TRUE_PREFIX.includes(text.charAt(0));

export const RowInputBoolean = (props: Props) => (
    <div className={[s.container].join(" ")}>
        <Select<boolean>
            value={isBoolean(props.value) ? true : false}
            setValue={(bool) => {
                props.setValue(bool ? "TRUE" : "FALSE");
            }}
            options={[
                { value: true, label: "TRUE" },
                { value: false, label: "FALSE" },
            ]}
            style={Select.style.flat}
            size={Select.size.small}
        />
    </div>
);
