import {
    AppStorage,
    StorageContext,
} from "@tableplus/toolkit/src/storage/storage";
import "external/external";
import { App } from "app/app/app";
import "index.scss";
import React from "react";
import { DndProvider } from "react-dnd-multi-backend";
import HTML5toTouch from "react-dnd-multi-backend/dist/esm/HTML5toTouch";
import ReactDOM from "react-dom";
import { PlatformContext, usePlatformProvider } from "utils/platform";
import { migrateStorage } from "./utils/migrate";

export const storage = new AppStorage("diagram");
migrateStorage(storage);

const Root = () => {
    const platform = usePlatformProvider();
    return (
        <React.StrictMode>
            <StorageContext.Provider value={storage}>
                <DndProvider options={HTML5toTouch}>
                    <PlatformContext.Provider value={platform}>
                        <App />
                    </PlatformContext.Provider>
                </DndProvider>
            </StorageContext.Provider>
        </React.StrictMode>
    );
};

ReactDOM.render(<Root />, document.getElementById("root"));
