import { EditDiagram } from "diagram/edit/edit";
import React from "react";
import { debugLog } from "utils/debug";
import { SelectId, SetSelectIds } from "../../select";
import { getDragBox, setDragBoxStyles } from "./box";
import s from "./drag.module.css";
import { resetTempSelect, setTempSelect } from "./element";
import { getOverlapIds } from "./overlap";

export interface SelectBox {
    top: number;
    left: number;
    width: number;
    height: number;
}

export interface SelectPosition {
    top: number;
    left: number;
}

interface Props {
    diagram: EditDiagram;
    setSelectIds: SetSelectIds;
}
type ContainerRef = React.RefObject<HTMLDivElement>;

const setContainerStyles = (ref: ContainerRef, isDragging: boolean): void => {
    const { current: element } = ref;
    if (element === null) throw Error("containerRef is null");
    // Win table's z index, which is 1
    element.style.zIndex = isDragging ? "2" : "0";
};

// For stable identities
const emptyIds: SelectId[] = [];

const SelectDragLayerPure = (props: Props) => {
    debugLog("CanvasSelectDragPure");
    const start = React.useRef<null | SelectPosition>(null);
    const isDraggingRef = React.useRef(false);
    const boxRef = React.useRef<HTMLDivElement>(null);
    const containerRef = React.useRef<HTMLDivElement>(null);
    const spaceLockRef = React.useRef<boolean>(false);
    return (
        <div
            className={s.container}
            tabIndex={0}
            onKeyDown={(event: React.KeyboardEvent) => {
                if (event.key === " ") spaceLockRef.current = true;
            }}
            onKeyUp={(event: React.KeyboardEvent) => {
                if (event.key === " ") spaceLockRef.current = false;
            }}
            onMouseDown={(event: React.MouseEvent) => {
                if (spaceLockRef.current === true) return;
                const { offsetX, offsetY } = event.nativeEvent;
                start.current = { left: offsetX, top: offsetY };
                isDraggingRef.current = true;
                // Set drag styles
                setContainerStyles(containerRef, true);
                setDragBoxStyles(boxRef, "start");
                // Reset ids
                props.setSelectIds(emptyIds);
            }}
            onMouseMove={(event: React.MouseEvent) => {
                if (isDraggingRef.current === false) return;
                const box = getDragBox(start, event);
                // Set styles
                setDragBoxStyles(boxRef, box);
                // Set select state (but not set select ids to avoid React's
                // re-renders)
                setTempSelect(box, props.diagram);
            }}
            onMouseUp={(event: React.MouseEvent) => {
                if (isDraggingRef.current === false) return;
                isDraggingRef.current = false;
                // Reset drag styles
                resetTempSelect();
                setContainerStyles(containerRef, false);
                setDragBoxStyles(boxRef, "stop");
                // Set ids
                const box = getDragBox(start, event);
                const ids = getOverlapIds({ diagram: props.diagram }, box);
                props.setSelectIds(ids);
                // Done
                start.current = null;
            }}
            ref={containerRef}
        >
            <div ref={boxRef} className={s.box}>
                <div className={s.boxBg} />
            </div>
        </div>
    );
};

export const SelectDragLayer = React.memo(SelectDragLayerPure);
