import { Switcher } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { UndoAction } from "diagram/state/undo/undo";
import React from "react";
import { HeaderButtonProps } from "./header";

interface Props<T> extends HeaderButtonProps {
    past: T[];
    future: T[];
    dispatch: React.Dispatch<UndoAction>;
}

const setValue = <T,>(props: Props<T>) => (value: string) => {
    switch (value) {
        case "undo":
            return props.dispatch({ type: "undo" });
        case "redo":
            return props.dispatch({ type: "redo" });
        default:
            throw Error(`Unknown value: "${value}"`);
    }
};

const undo = { key: "undo", icon: icon.undo, value: "undo" };
const redo = { key: "redo", icon: icon.redo, value: "redo" };

export const HeaderHistory = <T,>(props: Props<T>) => (
    <Switcher<string>
        value={""}
        setValue={setValue(props)}
        options={[
            { ...undo, disabled: props.past.length === 0 },
            { ...redo, disabled: props.future.length === 0 },
        ]}
        size={props.buttonSize}
    />
);
