import React from "react";
import { useDragLayer, DragLayerMonitor } from "react-dnd";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { Icon } from "@moai/core";
import s from "./layer.module.scss";
import { RowDragItem } from "../drag";
import { debugLog } from "utils/debug";

interface DraggingProps {
    item: RowDragItem;
    top: number;
    left: number;
}

const collect = (monitor: DragLayerMonitor): DraggingProps | null => {
    if (!monitor.isDragging()) return null;
    if (monitor.getItemType() !== "row") return null;
    const offset = monitor.getClientOffset();
    const item = monitor.getItem();
    if (item === null || offset === null) return null;
    return { item, top: offset.y, left: offset.x };
};

interface InitialState {
    scrollLeft: number;
    scrollTop: number;
    top: number;
    left: number;
}

interface Props {
    viewportRef: React.RefObject<HTMLDivElement>;
}

const getInitialState = (
    viewportRef: React.RefObject<HTMLDivElement>,
    sourceRef: React.RefObject<HTMLDivElement>
): InitialState => {
    const viewport = viewportRef.current;
    if (viewport === null) throw Error("Viewport is not defined");
    const source = sourceRef.current;
    if (source === null) throw Error("Source drag is not defined");
    const box = source.getBoundingClientRect();
    const [top, left] = [box.top + box.height / 2, box.left + box.width / 2];
    const { scrollTop, scrollLeft } = viewport;
    return { top, left, scrollTop, scrollLeft };
};

const ToIconPure = ({ top, left }: { top: number; left: number }) => (
    <div style={{ color: "var(--accent-2)" }}>
        <div className={s.icon} style={{ top: top - 8, left: left - 8 }}>
            <Icon path={icon.locate} size={16} />
        </div>
    </div>
);

const ToIcon = React.memo(ToIconPure);

const RowDragLayerPure = (props: Props) => {
    debugLog("RowDragLayer");

    const dragging = useDragLayer(collect);
    const initialRef = React.useRef<InitialState | null>(null);

    // Reset initial state based on is dragging or not
    const dragTargetRef = dragging?.item.elementRef;
    const { viewportRef } = props;
    React.useEffect(() => {
        initialRef.current =
            dragTargetRef === undefined
                ? null
                : getInitialState(viewportRef, dragTargetRef);
    }, [viewportRef, dragTargetRef]);

    const initial = initialRef.current;
    if (dragging === null || initial === null) return null;
    const viewport = props.viewportRef.current;
    if (viewport === null) throw Error("viewport is not defined");

    const diffLeft = viewport.scrollLeft - initial.scrollLeft;
    const diffTop = viewport.scrollTop - initial.scrollTop;

    return (
        <div className={s.container}>
            <svg className={s.svg}>
                <line
                    className={s.line}
                    x1={initial.left - diffLeft}
                    y1={initial.top - diffTop}
                    x2={dragging.left}
                    y2={dragging.top}
                />
            </svg>
            <ToIcon top={dragging.top} left={dragging.left} />
        </div>
    );
};

export const RowDragLayer = React.memo(RowDragLayerPure);
