import { getEdgeFromAttributes } from "app/edge/from";
import { getEdgeState } from "app/edge/state/state";
import { getEdgeToAttributes } from "app/edge/to";
import { getEdgeSubElement } from "app/edge/utils";
import { RelatedEdge } from "app/view/canvas/canvas";
import { EditTable } from "diagram/edit/edit";
import { TablePosition } from "diagram/edit/layout";
import { DraggableEventHandler } from "react-draggable";

interface Props {
    table: EditTable;
    relatedEdges: RelatedEdge[];
}

const setAttributes = (
    attributes: { [key: string]: string },
    element: Element
) => {
    Object.entries(attributes).forEach(([key, value]) => {
        element.setAttribute(key, value);
    });
};

const updateEdgeElements = (table: EditTable) => (relatedEdge: RelatedEdge) => {
    const { edge, table: related } = relatedEdge;
    const isTo = edge.to.table === table.id;
    const [fromTable, toTable] = isTo ? [related, table] : [table, related];
    const { path, points } = getEdgeState({ edge, fromTable, toTable });
    // Path
    getEdgeSubElement(edge.id, "path-main").setAttribute("d", path.main);
    // From
    points.from.inners.forEach((point, index) => {
        const element = getEdgeSubElement(edge.id, "from", index);
        const attributes = getEdgeFromAttributes(point);
        setAttributes(attributes, element);
    });
    // To
    points.to.inners.forEach((point, index) => {
        const element = getEdgeSubElement(edge.id, "to", index);
        const attributes = getEdgeToAttributes(points.to.side, point);
        setAttributes(attributes, element);
    });
    // Highlight
    try {
        const highlight = getEdgeSubElement(edge.id, "highlight");
        highlight.setAttribute("path", "");
    } catch (e) {
        // Nothing, it's ok to fail when highlight is not rendered
    }
};

export const onTableDragMove = (props: Props): DraggableEventHandler => {
    return (_event, data) => {
        const position: TablePosition = {
            left: props.table.position.left + data.x,
            top: props.table.position.top + data.y,
        };
        const table = { ...props.table, position };
        props.relatedEdges.forEach(updateEdgeElements(table));
    };
};
