import {
    Border,
    Button,
    ButtonSize,
    DivPx,
    Input,
    InputSize,
} from "@moai/core";
import { Theme } from "@tableplus/toolkit/src/prefs/theme/theme";
import { SetState } from "@tableplus/toolkit/src/types/types";
import { SetSelectIds } from "app/view/select/select";
import { EditDiagram } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { UndoAction } from "diagram/state/undo/undo";
import React from "react";
import { debugLog } from "utils/debug";
import { Platform, usePlatform } from "utils/platform";
import { HeaderAddTable } from "./add";
import { HeaderExport } from "./export/export";
import { HeaderHistory } from "./history";
import { HeaderLayout } from "./layout/layout";
import { HeaderSample } from "./sample/sample";
import { HeaderSearch } from "./search";
import s from "./style/header.module.scss";
import sMac from "./style/mac.module.scss";
import { HeaderTheme } from "./theme";
import { HeaderZoom } from "./zoom";

export interface HeaderPrimaryProps {
    // Diagram
    diagram: EditDiagram;
    diagramPast: EditDiagram[];
    diagramFuture: EditDiagram[];
    diagramDispatch: React.Dispatch<DiagramAction | UndoAction>;
    // View
    viewportRef: React.RefObject<HTMLDivElement>;
    zoom: number;
    setZoom: SetState<number>;
    setTableVisible: React.Dispatch<React.SetStateAction<boolean>>;
    setIsRowsExpand: React.Dispatch<React.SetStateAction<boolean>>;
    // Select
    setSelectIds: SetSelectIds;
    // App
    theme: Theme;
    setTheme: SetState<Theme>;
}

export interface HeaderButtonProps {
    buttonSize: ButtonSize;
}

export interface HeaderInputProps {
    inputSize: InputSize;
}

const platformStyle: Record<Platform, string> = {
    web: "",
    mac: sMac.container,
};

const getSize = (platform: Platform): [ButtonSize, InputSize] => {
    switch (platform) {
        case "mac":
            return [Button.size.small, Input.size.small];
        default:
            return [Button.size.medium, Input.size.medium];
    }
};

const getBigDiv = (platform: Platform): JSX.Element => {
    const border = platform === "web" ? <Border color="weak" /> : null;
    return (
        <>
            <DivPx size={16} /> {border} <DivPx size={16} />
        </>
    );
};

export const ViewHeaderPure = (props: HeaderPrimaryProps) => {
    debugLog("ViewHeader");

    const platform = usePlatform();
    const [buttonSize, inputSize] = getSize(platform);
    const bigDiv = getBigDiv(platform);

    return (
        <div className={[s.container, platformStyle[platform]].join(" ")}>
            <div className={s.platformPad} />
            <DivPx size={16} />
            {platform === "web" && (
                <>
                    <HeaderSample diagramDispatch={props.diagramDispatch} />
                    {bigDiv}
                </>
            )}
            <HeaderAddTable
                buttonSize={buttonSize}
                tables={props.diagram.tables}
                diagramDispatch={props.diagramDispatch}
                setSelectIds={props.setSelectIds}
            />
            <DivPx size={16} />
            <HeaderHistory
                buttonSize={buttonSize}
                past={props.diagramPast}
                future={props.diagramFuture}
                dispatch={props.diagramDispatch}
            />
            {bigDiv}
            <HeaderSearch
                inputSize={inputSize}
                diagram={props.diagram}
                setSelectIds={props.setSelectIds}
            />
            {bigDiv}
            <HeaderLayout
                buttonSize={buttonSize}
                setIsRowsExpand={props.setIsRowsExpand}
                diagramDispatch={props.diagramDispatch}
            />
            <DivPx size={16} />
            <HeaderZoom
                buttonSize={buttonSize}
                viewportRef={props.viewportRef}
                zoom={props.zoom}
                setZoom={props.setZoom}
            />
            <DivPx size={16} />
            <HeaderExport
                buttonSize={buttonSize}
                diagram={props.diagram}
                setTableVisible={props.setTableVisible}
            />
            {platform === "web" && (
                <>
                    {bigDiv}
                    <HeaderTheme
                        buttonSize={buttonSize}
                        theme={props.theme}
                        setTheme={props.setTheme}
                    />
                </>
            )}
            <DivPx size={16} />
        </div>
    );
};

export const ViewHeader = React.memo(ViewHeaderPure);
