import { RelatedEdge } from "app/view/canvas/canvas";
import { SetSelectIds } from "app/view/select/select";
import { EditColumn, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { TableActive } from "./active/active";
import { TableInactive } from "./inactive/inactive";
import { getTableElementId } from "./utils";

interface Props {
    // Table
    table: EditTable;
    // Diagram
    diagramDispatch: React.Dispatch<DiagramAction>;
    relatedEdges: RelatedEdge[];
    columns: EditColumn[];
    isRowsExpand: boolean;
    // Select
    isSelected: boolean;
    setSelectId: SetSelectIds;
    // View
    viewportRef: React.RefObject<HTMLDivElement>;
    zoom: number;
    forceVisible: boolean;
}

const observerCallback = (
    setIsVisible: React.Dispatch<React.SetStateAction<boolean>>
) => (entries: IntersectionObserverEntry[]) => {
    entries.forEach((entry) => {
        setIsVisible(entry.isIntersecting);
    });
};

const TablePure = (props: Props) => {
    const ref = React.useRef<HTMLDivElement>(null);
    const [selfVisible, setSelfVisible] = React.useState(false);

    React.useEffect(() => {
        const callback = observerCallback(setSelfVisible);
        const observer = new IntersectionObserver(callback, {
            threshold: 0,
            root: props.viewportRef.current,
        });
        if (ref.current === null) throw Error("ref is null");
        observer.observe(ref.current);
        return () => observer.disconnect();
    }, [props.viewportRef]);

    const isVisible = selfVisible || props.forceVisible;

    return (
        <div
            id={getTableElementId(props.table.id)}
            style={{
                position: "absolute",
                top: props.table.position.top,
                left: props.table.position.left,
                // Needs to be set here because this is top-level table
                zIndex: props.isSelected ? 1 : 0,
            }}
            ref={ref}
            // Temporary use to avoid using previous table size for new layout
            data-name={props.table.value.name}
            // Edges will watch this attribute to re-draw themselves
            data-is-visible={isVisible}
        >
            {isVisible ? (
                <TableActive
                    table={props.table}
                    // Diagram
                    columns={props.columns}
                    diagramDispatch={props.diagramDispatch}
                    relatedEdges={props.relatedEdges}
                    isRowsExpand={props.isRowsExpand}
                    // Select
                    isSelected={props.isSelected}
                    setSelectId={props.setSelectId}
                    // View
                    zoom={props.zoom}
                />
            ) : (
                <TableInactive
                    isRowsExpand={props.isRowsExpand}
                    table={props.table}
                />
            )}
        </div>
    );
};

export const Table = React.memo(TablePure);
