import { EditEdge } from "diagram/edit/edit";
import * as React from "react";
import s from "./edge.module.scss";
import { EdgePoint, EdgePointsEnd } from "./state/point";
import { getEdgeSubElementId } from "./utils";

interface Props {
    edge: EditEdge;
    points: EdgePointsEnd;
}

type Side = "left" | "right";

const rotateInner = (side: Side, x: number, y: number) =>
    side === "left" ? `rotate(180, ${x}, ${y})` : "";

export const getEdgeToAttributes = (side: Side, { x, y }: EdgePoint) => ({
    transform: rotateInner(side, x, y),
    // the arrow point from right to left by default
    points: [
        `${x + 9},${y - 4}`, // top right
        `${x + 9},${y + 4}`, // bottom right
        `${x + 1},${y}`, // middle left
    ].join(" "),
});

export const EdgeTo = (props: Props) => (
    <>
        {props.points.inners.map((point, index) => (
            <polygon
                key={index}
                className={s.to}
                id={getEdgeSubElementId(props.edge.id, "to", index)}
                {...getEdgeToAttributes(props.points.side, point)}
            />
        ))}
    </>
);
