import { SelectPosition, SelectBox } from "./drag";

export const getDragBox = (
    startRef: React.RefObject<SelectPosition>,
    event: React.MouseEvent
): SelectBox => {
    const start = startRef.current;
    if (start === null) throw Error("start is null");
    const current = {
        left: event.nativeEvent.offsetX,
        top: event.nativeEvent.offsetY,
    };
    const top = Math.min(start.top, current.top);
    const right = Math.max(start.left, current.left);
    const bottom = Math.max(start.top, current.top);
    const left = Math.min(start.left, current.left);
    const [width, height] = [right - left, bottom - top];
    return { top, left, width, height };
};

export const setOpacity = (element: HTMLDivElement, opacity: string): void => {
    element.style.opacity = opacity;
};

const emptyBox: SelectBox = { top: 0, left: 0, width: 0, height: 0 };

export const setSize = (element: HTMLDivElement, box: SelectBox): void => {
    element.style.width = `${box.width}px`;
    element.style.height = `${box.height}px`;
    element.style.transform = `translate(${box.left}px, ${box.top}px)`;
};

export const setDragBoxStyles = (
    boxRef: React.RefObject<HTMLDivElement>,
    box: SelectBox | "start" | "stop"
): void => {
    const { current: element } = boxRef;
    if (element === null) throw Error("boxRef is null");
    if (box === "start") {
        setSize(element, emptyBox);
        setOpacity(element, "1");
    } else if (box === "stop") {
        setOpacity(element, "0");
    } else {
        setSize(element, box);
    }
};
