import { DivPx } from "@moai/core";
import { EditColumn, EditRef, EditRow } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { debugLog } from "utils/debug";
import { useRowDropTarget } from "./drag/drop/drop";
import { RowDragSource } from "./drag/source/source";
import { RowFieldInput } from "./field/field";
import { useRowKeyUpListeners } from "./key";
import { RowRemoveButton } from "./remove/remove";
import s from "./row.module.scss";

interface Props {
    // row
    row: EditRow;
    edge: EditRef | null;
    // table
    tableId: number;
    isEdit: boolean;
    isExpand: boolean;
    // diagram
    columns: EditColumn[];
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const RowPure = (props: Props) => {
    debugLog("Table", props.tableId, "Row", props.row.id, props.row.value.id);

    const [isOver, dropRef] = useRowDropTarget({
        tableId: props.tableId,
        rowId: props.row.id,
    });

    return (
        <div
            {...useRowKeyUpListeners({
                diagramDispatch: props.diagramDispatch,
                tableId: props.tableId,
                rowId: props.row.id,
            })}
            className={`${s.container} ${isOver ? s.over : ""}`}
            ref={dropRef}
        >
            <DivPx size={8} />

            {/* REMOVE */}
            {props.isEdit && (
                <div className="row-control">
                    <RowRemoveButton
                        tableId={props.tableId}
                        row={props.row}
                        diagramDispatch={props.diagramDispatch}
                    />
                </div>
            )}

            {/* FIELDS */}
            {props.columns
                .slice(0, props.isExpand ? undefined : 2)
                .map((column, index) => (
                    <div className="row-control" key={column.id}>
                        <RowFieldInput
                            tableId={props.tableId}
                            row={props.row}
                            column={column}
                            diagramDispatch={props.diagramDispatch}
                            isRight={index === 1}
                        />
                    </div>
                ))}

            {/* DRAG */}
            {props.isEdit && (
                <RowDragSource
                    tableId={props.tableId}
                    rowId={props.row.id}
                    diagramDispatch={props.diagramDispatch}
                    edge={props.edge}
                />
            )}

            <DivPx size={8} />
        </div>
    );
};

export const Row = React.memo(RowPure);
