import { ParseJsonPayload } from "diagram/state/reducer/parse/json";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";

interface Props {
    dispatch: React.Dispatch<DiagramAction>;
}

const catchPaste = (props: Props, event: ClipboardEvent) => {
    if (event.target instanceof HTMLInputElement) return;
    if (event.target instanceof HTMLTextAreaElement) return;
    const payload: ParseJsonPayload = {
        text: event.clipboardData?.getData("text/plain") ?? "",
        isAppend: true,
    };
    props.dispatch({ type: "parse-json", payload });
};

export const useCanvasCatchPaste = (props: Props) => {
    const { dispatch } = props;
    React.useEffect(() => {
        const listener = (event: ClipboardEvent) => {
            return catchPaste({ dispatch }, event);
        };
        document.addEventListener("paste", listener);
        return () => document.removeEventListener("paste", listener);
    }, [dispatch]);
};
