import { Input, outline } from "@moai/core";
import * as React from "react";
import { isJustLeftEdit, setJustLeftEdit } from "./leave";
import s from "./string.module.scss";

interface Props {
    value: string;
    setValue: (value: string) => void;
}

interface InternalProps extends Props {
    isEdit: boolean;
    setIsEdit: React.Dispatch<React.SetStateAction<boolean>>;
    viewRef: React.RefObject<HTMLDivElement>;
}

const focusView = (props: InternalProps) => {
    window.setTimeout(() => {
        const view = props.viewRef.current;
        if (view === null) throw Error("View is null");
        view.focus();
    }, 0); // Can only focus view after React's render
};

const Edit = (props: InternalProps) => (
    <div className={s.edit}>
        <Input
            key={props.value} // uncontrolled
            defaultValue={props.value}
            style={Input.style.flat}
            size={Input.size.small}
            autoFocus
            onBlur={(event) => {
                props.setValue(event.currentTarget.value);
                props.setIsEdit(false);
            }}
            onKeyDown={(event) => {
                if (event.key !== "Tab") return;
                setJustLeftEdit();
            }}
            onKeyUp={(event) => {
                if (event.key !== "Enter") return;
                props.setValue(event.currentTarget.value);
                props.setIsEdit(false);
                focusView(props);
            }}
        />
    </div>
);

const viewClass = [
    s.view,
    outline.always,
    Input.size.small.main,
    "react-draggable-cancel",
    "row-input-string-view",
].join(" ");

const ViewValue = ({ value }: { value: string }) => {
    if (value.length !== 0) return <span>{value}</span>;
    return <span className={s.empty}>&lt;empty&gt;</span>;
};

const View = (props: InternalProps) => (
    <div
        tabIndex={0}
        children={<ViewValue value={props.value} />}
        className={viewClass}
        onDoubleClick={() => {
            props.setIsEdit(true);
        }}
        onFocus={() => {
            if (isJustLeftEdit()) props.setIsEdit(true);
        }}
        onKeyUp={(event) => {
            if (event.key !== "Enter") return;
            props.setIsEdit(true);
        }}
        ref={props.viewRef}
    />
);

export const RowInputString = (props: Props) => {
    const [isEdit, setIsEdit] = React.useState(false);
    const viewRef = React.useRef<HTMLDivElement>(null);
    const internal: InternalProps = { ...props, isEdit, setIsEdit, viewRef };

    return (
        <div className={[s.container].join(" ")}>
            {isEdit ? <Edit {...internal} /> : <View {...internal} />}
        </div>
    );
};
