import { background, borderColor } from "@moai/core";
import { EditTable } from "diagram/edit/edit";
import * as React from "react";
import { getTableEstimatedSize } from "../utils";
import s from "./inactive.module.css";

interface Props {
    table: EditTable;
    isRowsExpand: boolean;
}

const cls = [s.container, background.primary, borderColor.strong].join(" ");

export const TableInactive = (props: Props) => (
    <div
        className={cls}
        style={getTableEstimatedSize(props.table, props.isRowsExpand)}
    />
);
