import { TABLE_CLIENT_WIDTH, TABLE_MARGIN_DOUBLE } from "app/table/utils";
import * as Edit from "diagram/edit/edit";
import { DiagramSize, TablePosition } from "diagram/edit/layout";
import { getNumberId, getStringId } from "../id";
import { DiagramReducer } from "../reducer";
import { getDefaultRow } from "../row/add";

export interface AddTablePayload {
    id?: number;
}

type Payload = AddTablePayload;

const toMaxLeft = (prev: number, table: Edit.EditTable) =>
    Math.max(table.position.left, prev);

const leftToRight = TABLE_CLIENT_WIDTH + TABLE_MARGIN_DOUBLE;

// For now we will just put it on the right side of the most right table
export const getTableNewPosition = (
    tables: Edit.EditTable[]
): TablePosition => {
    const maxLeft = tables.reduce(toMaxLeft, 0);
    return { top: TABLE_MARGIN_DOUBLE, left: maxLeft + leftToRight };
};

const getNewSize = (
    size: DiagramSize,
    position: TablePosition
): DiagramSize => {
    const width = Math.max(size.width, position.left + leftToRight);
    return width === size.width ? size : { ...size, width };
};

export const addTable: DiagramReducer<Payload> = (diagram, payload) => {
    // value
    const valueIds = diagram.tables.map((table) => table.value.id);
    const name = getStringId(valueIds, "table");
    const row = getDefaultRow([]);
    // edit table
    const table: Edit.EditTable = {
        id: payload.id ?? getNumberId(diagram.tables.map((table) => table.id)),
        value: { name, id: name, schema: "new", rows: [row] },
        position: getTableNewPosition(diagram.tables),
    };
    return {
        ...diagram,
        tables: diagram.tables.concat(table),
        size: getNewSize(diagram.size, table.position),
    };
};
