import { background, Border } from "@moai/core";
import { useTheme } from "@tableplus/toolkit/src/prefs/theme/theme";
import { RowDragLayer } from "app/row/drag/layer/layer";
import { useDiagram } from "diagram/state/use-diagram";
import React from "react";
import { Canvas } from "./canvas/canvas";
import { useDragToScroll } from "./drag/use-drag";
import { ViewHeader } from "./header/header";
import { ViewProgress } from "./progress/progress";
import { useCanvasCatchCopy } from "./select/canvas/copy";
import { catchDelete } from "./select/canvas/delete";
import { useCanvasCatchPaste } from "./select/canvas/paste";
import { useSelect } from "./select/select";
import s from "./view.module.scss";

export type Mode = "design" | "present" | "structure";

export const View = () => {
    const diagram = useDiagram();
    const [zoom, setZoom] = React.useState<number>(1);
    const viewportRef = React.useRef<HTMLDivElement>(null);
    const select = useSelect();
    const theme = useTheme();
    const [tableVisible, setTableVisible] = React.useState(false);
    const [isRowsExpand, setIsRowsExpand] = React.useState(false);

    useCanvasCatchCopy({ diagram: diagram.history.present, ids: select.ids });
    useCanvasCatchPaste({ dispatch: diagram.dispatch });

    // Drag to scroll viewport
    useDragToScroll(viewportRef);

    return (
        <div className={s.container}>
            <div className={s.header}>
                <ViewHeader
                    // Diagram
                    diagram={diagram.history.present}
                    diagramPast={diagram.history.past}
                    diagramFuture={diagram.history.future}
                    diagramDispatch={diagram.dispatch}
                    // Theme
                    theme={theme.theme}
                    setTheme={theme.setTheme}
                    // View
                    viewportRef={viewportRef}
                    zoom={zoom}
                    setZoom={setZoom}
                    setTableVisible={setTableVisible}
                    setIsRowsExpand={setIsRowsExpand}
                    // Select
                    setSelectIds={select.setIds}
                />
            </div>
            <Border color="strong" />
            <div className={`${s.body} ${background.secondary}`}>
                <div
                    className={s.viewport}
                    tabIndex={0}
                    ref={viewportRef}
                    onKeyUp={(event) => {
                        catchDelete({
                            ids: select.ids,
                            diagramDispatch: diagram.dispatch,
                            event,
                        });
                    }}
                >
                    <Canvas
                        // Diagram
                        diagram={diagram.history.present}
                        diagramDispatch={diagram.dispatch}
                        // View
                        isRowsExpand={isRowsExpand}
                        zoom={zoom}
                        viewportRef={viewportRef}
                        forceVisible={tableVisible}
                        // Select
                        selectedRefs={select.refs}
                        selectedTables={select.tables}
                        setSelectIds={select.setIds}
                    />
                </div>
                <ViewProgress />
            </div>
            <RowDragLayer viewportRef={viewportRef} />
        </div>
    );
};
