import { DiagramReducer } from "../reducer";
import { parseJson, ParseJsonPayload } from "./json";

export type DiagramParseActions = {
    type: "parse-json";
    payload: ParseJsonPayload;
};

export const diagramParseReducer: DiagramReducer = (diagram, action) => {
    switch (action.type) {
        case "parse-json":
            return parseJson(diagram, action.payload);
        default:
            return diagram;
    }
};
