import { Diagram } from "diagram/diagram";
import { makeEditFromDiagram } from "diagram/edit/convert/to";
import { validateSchema } from "diagram/schema/schema";
import { validateSemantic } from "diagram/schema/semantic";
import { DiagramReducer } from "../reducer";
import { appendDiagram } from "./append";

export interface ParseJsonPayload {
    text: string;
    isAppend: boolean;
}

type Payload = ParseJsonPayload;

const getErrorMessage = (error: any): string => {
    if (!(error instanceof Error)) throw error;
    return [
        "There was an error while parsing your json:\n",
        `${error.name}: ${error.message}`,
    ].join(" ");
};

/** text (json) => Diagram => EditDiagram */
export const parseJson: DiagramReducer<Payload> = (prev, payload) => {
    try {
        const diagram = JSON.parse(payload.text) as Diagram;
        validateSchema(diagram);
        validateSemantic(diagram);
        const target = makeEditFromDiagram(diagram);
        if (!payload.isAppend) return target;
        return appendDiagram(prev, target);
    } catch (error) {
        window.alert(getErrorMessage(error));
        return prev;
    }
};
