import { Button } from "@moai/core";
import { icon } from "@tableplus/toolkit/src/icon/icon";
import { getTableElement } from "app/table/utils";
import { EditTable } from "diagram/edit/edit";
import { getNumberId } from "diagram/state/reducer/id";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { SetSelectIds } from "../select/select";
import { HeaderButtonProps } from "./header";

interface Props extends HeaderButtonProps {
    tables: EditTable[];
    diagramDispatch: React.Dispatch<DiagramAction>;
    setSelectIds: SetSelectIds;
}

// @TODO unify this and the one at "search"?
const scrollOption: ScrollIntoViewOptions = {
    behavior: "smooth",
    block: "center",
    inline: "center",
};

export const HeaderAddTable = (props: Props) => {
    const onClick = () => {
        const id = getNumberId(props.tables.map((t) => t.id));
        props.diagramDispatch({ type: "add-table", payload: { id } });
        window.setTimeout(() => {
            props.setSelectIds([{ type: "table", value: id }]);
            getTableElement(id).scrollIntoView(scrollOption);
        }, 0); // Run after React's render new table
    };
    return (
        <Button
            onClick={onClick}
            size={props.buttonSize}
            icon={icon.plus}
            children="Table"
        />
    );
};
