import { EditEdge } from "diagram/edit/edit";
import * as React from "react";
// import { PairMap } from "./pair-map";
import { EdgePoint } from "./point";

// The purpose of this module is to avoid the situation where the "x" of "mids"
// points of some edges are the same thus it's not clear which edge ends at
// which row. For example, in the figure below, it's not clear whether A or C
// is connected to X:
//
// A >-----+
// B       |
// C >-----+
//         |
//         |
//         +----> X
//         |      Y
//         +----> Z
//

interface UseEdgesMidsProps {
    edges: EditEdge[];
}

export interface EdgesMids {
    edgesMap: Map<number, { mid: number; detail: EditEdge }>;
    // usedMids: PairMap<number, Set<number>>;
    usedMids: Set<number>;
}

export const useEdgesMids = (props: UseEdgesMidsProps) => {
    const edgesMids = React.useRef<EdgesMids>({
        edgesMap: new Map(),
        // usedMids: new PairMap(),
        usedMids: new Set(),
    });

    // Remove outdated edges
    React.useEffect(() => {
        const ids = new Set(props.edges.map((edge) => edge.id));
        edgesMids.current.edgesMap.forEach((edge, id) => {
            if (ids.has(id)) return;
            // id is not inside ids ===> removed ref
            // edgesMids.current.usedMids
            //     .get(edge.detail.from.table, edge.detail.to.table)
            //     ?.delete(edge.mid);
            edgesMids.current.usedMids.delete(edge.mid);
            edgesMids.current.edgesMap.delete(id);
        });
    }, [props.edges]);

    return edgesMids;
};

interface CollisionProps {
    edgesMids: EdgesMids;
    edge: EditEdge;
    midPoints: EdgePoint[];
}

const getClosestAvailable = (set: Set<number>, start: number): number => {
    let result = start;
    let i = 1;
    while (set.has(result)) {
        result = start + i * 8;
        i = i > 0 ? -i : -i + 1;
    }
    return result;
};

export const avoidMidCollision = (props: CollisionProps): EdgePoint[] => {
    const { edgesMap, usedMids } = props.edgesMids;
    const next = props.midPoints[0].x; // All mid points have the same "x"
    const prev = edgesMap.get(props.edge.id)?.mid;

    // Nothing changes
    if (prev === next) return props.midPoints;

    // const [from, to] = [props.edge.from.table, props.edge.to.table];
    // const set = usedMids.getOrSet(from, to, new Set());
    const set = usedMids;

    // Remove previous value if set
    if (prev !== undefined) set.delete(prev);

    if (set.has(next)) {
        // Collision happens! Must choose new x
        const x = getClosestAvailable(set, next);
        edgesMap.set(props.edge.id, { detail: props.edge, mid: x });
        set.add(x);
        return props.midPoints.map((point) => ({ ...point, x }));
    } else {
        // No collision. Just record the new one and move on
        edgesMap.set(props.edge.id, { detail: props.edge, mid: next });
        set.add(next);
        return props.midPoints;
    }
};
