import { EditEdge } from "diagram/edit/edit";
import { RelatedEdge } from "app/view/canvas/canvas";

export type EdgeFromRowMap = Map<number, EditEdge>; // row id to edge from

export const getTableRowsEdgeMap = (
    tableId: number,
    edges: RelatedEdge[]
): EdgeFromRowMap => {
    const map: EdgeFromRowMap = new Map();
    edges.forEach((edge) => {
        if (edge.edge.from.table !== tableId) return;
        edge.edge.from.rows.forEach((rowId) => {
            map.set(rowId, edge.edge);
        });
    });
    return map;
};
