import { EditEdge } from "diagram/edit/edit";
import * as React from "react";
import s from "./edge.module.scss";
import { EdgePoint, EdgePointsEnd } from "./state/point";
import { getEdgeSubElementId } from "./utils";

interface Props {
    edge: EditEdge;
    points: EdgePointsEnd;
}

export const getEdgeFromAttributes = (point: EdgePoint) => ({
    cx: point.x.toString(),
    cy: point.y.toString(),
});

export const EdgeFrom = (props: Props) => (
    <>
        {props.points.inners.map((point, index) => (
            <circle
                key={index}
                id={getEdgeSubElementId(props.edge.id, "from", index)}
                className={s.from}
                {...getEdgeFromAttributes(point)}
                r="4"
            />
        ))}
    </>
);
