import {
    AppStorage,
    StorageContext,
} from "@tableplus/toolkit/src/storage/storage";
import { EditDiagram } from "diagram/edit/edit";
import React from "react";
import { usePlatform } from "utils/platform";
import { emptyDiagram, fallbackDiagram } from "./fallback";

const STORAGE_KEY = "diagram";

const parseErrorMessage = [
    "There was an error while parsing the locally saved diagram. The app will ",
    "now fallback to a sample diagram.\n\n",
    "Your previous diagram is still available in your localStorage under the ",
    `key "${STORAGE_KEY}-invalid". Please contact us if you need help `,
    "retrieving it.",
].join("");

const getDiagram = (storage: AppStorage): EditDiagram => {
    const stored = storage.getItem(STORAGE_KEY);
    if (stored === null) return fallbackDiagram;
    try {
        return JSON.parse(stored);
    } catch {
        storage.setItem(`${STORAGE_KEY}-invalid`, stored);
        window.alert(parseErrorMessage);
        return fallbackDiagram;
    }
};

const setDiagram = (storage: AppStorage, diagram: EditDiagram) => {
    storage.setItem(STORAGE_KEY, JSON.stringify(diagram));
};

interface DiagramStorageState {
    get: () => EditDiagram;
    set: (diagram: EditDiagram) => void;
}

export const useDiagramStorage = (): DiagramStorageState => {
    const storage = React.useContext(StorageContext);
    if (storage === null) throw Error("Storage context not found");
    const platform = usePlatform();

    return React.useMemo(() => {
        return platform === "web"
            ? {
                  get: () => getDiagram(storage),
                  set: (diagram: EditDiagram) => setDiagram(storage, diagram),
              }
            : {
                  get: () => emptyDiagram,
                  set: () => {},
              };
    }, [storage, platform]);
};
