import { EditRef } from "diagram/edit/edit";
import { SelectId, SetSelectIds } from "./select";

interface Props {
    tableId: number;
    refs: EditRef[];
    setSelectIds: SetSelectIds;
}

const isRelated = (target: number) => (ref: EditRef): boolean =>
    [ref.from.table, ref.to.table].includes(target);

type Event = React.MouseEvent<HTMLDivElement>;

export const getTableRelatedIds = (
    tableId: number,
    refs: EditRef[]
): SelectId[] => {
    const ids: SelectId[] = refs
        .filter(isRelated(tableId))
        .map((ref) => ({ type: "ref", value: ref.id }));
    ids.push({ type: "table", value: tableId });
    return ids;
};

export const selectOnTableFocus = (props: Props) => (event: Event) => {
    if (event.target instanceof HTMLInputElement) return;
    const ids = getTableRelatedIds(props.tableId, props.refs);
    props.setSelectIds(ids);
};
