import { getTableElement } from "app/table/utils";
import { EditDiagram } from "diagram/edit/edit";
import { SelectBox } from "./drag";
import { isTableOverlap } from "./overlap";
import { getEdgeElement } from "app/edge/utils";

export const DRAG_SELECT_CLASS = "temp-selected";

const setClass = (element: Element, isOverlap: boolean) => {
    if (isOverlap) {
        element.classList.add(DRAG_SELECT_CLASS);
    } else {
        element.classList.remove(DRAG_SELECT_CLASS);
    }
};

export const setTempSelect = (target: SelectBox, diagram: EditDiagram) => {
    const selected = new Set<number>();
    diagram.tables.forEach((table) => {
        const element = getTableElement(table.id);
        const { position } = table;
        const isOverlap = isTableOverlap({ target, element, position });
        if (isOverlap) selected.add(table.id);
        setClass(element, isOverlap);
    });
    diagram.refs.forEach((ref) => {
        const isOverlap =
            selected.has(ref.from.table) || selected.has(ref.to.table);
        const element = getEdgeElement(ref.id);
        setClass(element, isOverlap);
    });
};

export const resetTempSelect = () => {
    document.querySelectorAll(`.${DRAG_SELECT_CLASS}`).forEach((element) => {
        element.classList.remove(DRAG_SELECT_CLASS);
    });
};
