import { Input } from "@moai/core";
import { SetState } from "@tableplus/toolkit/src/types/types";
import { EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { UpdateTableNamePayload } from "diagram/state/reducer/table/update-name";
import React from "react";
import s from "./name.module.scss";
import { TABLE_CLIENT_WIDTH } from "app/table/utils";

interface Props {
    table: EditTable;
    diagramDispatch: React.Dispatch<DiagramAction>;
    setIsEdit: SetState<boolean>;
}

type FocusEvent = React.FocusEvent<HTMLInputElement>;

const updateTableName = (props: Props, event: FocusEvent) => {
    const { value } = event.currentTarget;
    if (value === props.table.value.name) return;
    const payload: UpdateTableNamePayload = { id: props.table.id, name: value };
    props.diagramDispatch({ type: "update-table-name", payload });
};

export const HeaderName = (props: Props) => {
    // Because HeaderName is draggable, this helps us identify if a drag
    // happened or just a click
    const offset = React.useRef({ x: 0, y: 0 });

    // This is the component's internal isEdit state, which decides whether to
    // show an Input or a div. It's different from props.isEdit, which is
    // the whole table's edit state
    const [isEdit, setIsEdit] = React.useState(false);
    return (
        <div
            className={s.container}
            style={{
                width: TABLE_CLIENT_WIDTH - 16,
            }}
        >
            {isEdit ? (
                <Input
                    key={props.table.value.name} // uncontrolled
                    defaultValue={props.table.value.name}
                    onBlur={(event) => {
                        updateTableName(props, event);
                        setIsEdit(false);
                    }}
                    onFocus={() => {
                        props.setIsEdit(false);
                    }}
                    style={Input.style.flat}
                    size={Input.size.small}
                    autoFocus
                />
            ) : (
                <div
                    className={[s.view, Input.size.small.main].join(" ")}
                    onMouseDown={(event) => {
                        offset.current.x = event.clientX;
                        offset.current.y = event.clientY;
                    }}
                    onClick={(event) => {
                        const { x: x1, y: y1 } = offset.current;
                        const { clientX: x2, clientY: y2 } = event;
                        // Only enter edit mode if this is not a drag
                        if (x1 === x2 && y1 === y2) setIsEdit(true);
                    }}
                >
                    {props.table.value.name}
                </div>
            )}
        </div>
    );
};
